<template>
	<!-- <section>

	</section> -->
	<metro_page>
        <!--  -->
        <metro_page_box class="search_partition">
            <metro_page_box_body>
	<!-- form -->
		<pm_form_render :model="formModel" :rules="rules" ref="demoForm" :formReadOnly="formReadOnly">
        	<pm_form_item row="1" labletext="" name="businessOtherNo" :span="8" xtype="text" lableWidth="100px" :readOnly='true'></pm_form_item>
			<pm_form_item row="1" labletext="" name="cstId" :span="8" xtype="select" lableWidth="100px" required></pm_form_item>
			<pm_form_item row="1" labletext="" name="businessType" :span="8" xtype="select" lableWidth="100px" :readOnly='true' required></pm_form_item>
			<pm_form_item row="2" labletext="" name="creatorName" :span="8" xtype="text" lableWidth="100px" :readOnly='true'></pm_form_item>
			<pm_form_item row="2" labletext="" name="createDate" :span="8" xtype="date" lableWidth="100px" required></pm_form_item>
			<pm_form_item row="2" labletext="" name="balanceWeightType" :span="8" xtype="radio" :radioGroup="group" lableWidth="100px" required ></pm_form_item>
			<!-- <pm_radio :menu="group"></pm_radio> -->
            <!-- <pm_form_item row="1" labletext="" name="test" :span="10" xtype="treeSelect" lableWidth="50px"></pm_form_item> -->
    	</pm_form_render>
            </metro_page_box_body>
        </metro_page_box>

		<!--  -->
        <metro_page_box>
            <!-- body -->
            <metro_page_box_body>
            <!-- toolbar -->
        <pm_tool_bar>
            <pm_toolButton ref="add" btnName ="" btnIcon ="el-icon-circle-plus-outline" :btnClickFunc ='addDetail' :btnDisabled="changeBtn"></pm_toolButton>
            <pm_toolButton ref="delete" btnName ="" btnIcon ="el-icon-remove-outline"  :btnClickFunc ='deleteDetail' :btnDisabled="changeBtn"></pm_toolButton>
            <pm_toolButton ref="clear" btnName ="" btnIcon ="el-icon-delete" :btnClickFunc ='clearDetail' :btnDisabled="changeBtn"></pm_toolButton>
        </pm_tool_bar>
            <pm_table ref="pmTable" :dataSource="dataSource" :config="config" :bottomHeight="168">
      			<pm_column prop="packNo" label="" width="180" placeholder=""></pm_column>
      			<pm_column prop="brandId" label="" width="180" placeholder required></pm_column>
      			<pm_column prop="textureId" label="" width="150" watchField="brandId"></pm_column>
      			<pm_column prop="spec" label="" width="150" placeholder></pm_column>
      			<pm_column prop="producingId" label="" width="180" placeholder></pm_column>
      			<pm_column prop="whsDayNum" label="" width="150" placeholder></pm_column>
      			<pm_column prop="whsFreeDayNum" label="" width="150" placeholder></pm_column>
      			<pm_column prop="costType" label="" width="150" required placeholder></pm_column>
      			<pm_column prop="unitprice" label="" width="150" placeholder></pm_column>
      			<pm_column prop="regularUnit" label="" width="120" placeholder watchField='costType' :canEdit='false' required></pm_column>
				<pm_column prop="numFin" label="" width="150" placeholder></pm_column>
      			<pm_column prop="netWeight" label="" width="150" placeholder></pm_column>
      			<pm_column prop="grossWeight" label="" width="150" placeholder></pm_column>
      			<pm_column prop="poundWeight" label="" width="150" placeholder></pm_column>
      			<pm_column prop="cstId" label="" width="200" placeholder></pm_column>
      			<pm_column prop="whsType" label="" width="120" placeholder></pm_column>
      			<pm_column prop="whsCalcStackId" label="" width="150" parentPropName='whsRoomId' childPropName='whsAreaId'></pm_column>
      			<pm_column prop="whsStackName" label="" width="120" placeholder></pm_column>
      			<pm_column prop="cardNo" label="" width="120" placeholder></pm_column>
      			<pm_column prop="remark" label="" width="120" placeholder></pm_column>
    		</pm_table>
			<pm_tool_bar :noBackground="true">
            <pm_toolButton ref="saveInfo" btnName ="" btnIcon ="el-icon-circle-check-outline" :btnClickFunc ='saveInfo' :btnDisabled="changeBtn"></pm_toolButton>
            <pm_toolButton ref="settle" btnName ="" btnIcon ="el-icon-sold-out"  :btnClickFunc ='settle'></pm_toolButton>
            </pm_tool_bar>
            </metro_page_box_body>
        </metro_page_box>
	</metro_page>
</template>

<script>
	import metro_page from '@/components/vitems/pageContainer/metro_page';
	import metro_page_box from '@/components/vitems/pageContainer/metro_page_box';
    import metro_page_box_tool_bar from '@/components/vitems/pageContainer/metro_page_box_tool_bar';
    import metro_page_box_body from '@/components/vitems/pageContainer/metro_page_box_body';
	import pm_form_render from '@/components/common/form/pm_form_render';
	import pm_form_item from '@/components/common/form/pm_form_item';
	import pm_toolButton from "@/components/common/button/pm_toolButton";
	import pm_tool_bar from "@/components/common/table/pm_tool_bar";
	import pm_table from "@/components/common/table/pm_table";
	import pm_column from "@/components/common/table/pm_column";
	import pm_radio from "@/components/common/radio/pm_radio";
	import metadata from "@/common/entities/Metadata";
	import cacheUtil from "@/common/utils/CacheUtil";
	import commonUtil from "@/common/utils/CommonUtils";
	import httpUtil from "@/common/utils/HttpUtil";
	import costBalanceAddManage from "@/views/cost/costBalanceAdd/CostBalanceAddManage";
	export default {
		components: {metro_page, metro_page_box, metro_page_box_tool_bar,pm_tool_bar,pm_toolButton,
			pm_table,pm_column,metro_page_box_body, pm_form_item,pm_radio},
		data: function(){
			return{
				dataSource: [],
				config: {
                	entity: metadata.regular,
                	allowEdit: true,
					multiple: true,
					showSummary: false,
            	},
				 rules: {
				},
				formReadOnly:false,
				counter:0,
				totalSize:0,
				group:[{key:1,value:""},{key:2,value:""},{key:3,value:""}],
				formModel:null,
				changeBtn:false,
			}
			
		},
		created(){
			this.formModel=this.getFormModel();
		},
		computed:{
  		},
		methods:{
			getFormModel(){
				var entity ={};	
				entity.businessOtherNo = this.$commonUtil.appConst.nullNo;
				entity.creatorName = cacheUtil.getUser().configUser.name;
				entity.businessType=5;
				entity.createDate=new Date();
        		return entity;
			},
			//
            initFormState: function(data) {
				//this.formModel=data;
				this.formModel.businessOtherNo=data.businessOtherNo;
				data.detail.forEach(element => {
					element.textureId=parseInt(element.material);
					element.producingId=parseInt(element.productPlace);
				});
				this.dataSource=data.detail;
				this.changeBtn=true;
				this.formReadOnly = true;
            },
			addDetail:function(){
				var $this = this;
				var param=this.formModel;
				if(param.cstId===undefined){
					this.$message.error('');
                	return;
				}
            	if(!$this.dataSource){
                   $this.dataSource = []; 
            	}
				var detail = {cstId:param.cstId,
								whsDayNum:0,
								whsFreeDayNum:0,
								numFin:0,
								unitprice:0,
								netWeight:0,
								grossWeight:0,
								poundWeight:0};
            	this.counter += 1;
            	detail.index = this.counter;
            	$this.dataSource =  $this.dataSource.concat(detail);
			},
			deleteDetail:function(){
				//
            	var rows = this.$refs.pmTable.getSelectedRows();
            	if(rows.length==0){
                	this.$message.error('');
                	return;
            	}
            	//
            	commonUtil.removeSelectedRows2(rows,this.dataSource);
			},
			clearDetail:function(){
				//
            	var rows = this.$refs.pmTable.getTableInfo();
            	//
            	commonUtil.removeSelectedRows2(rows,this.dataSource);
			},
			saveInfo:function(){
				var $this=this;
				var param=this.formModel;
				//
				param.detail=this.dataSource;
            	if(param.detail.length===0){
                	this.$message.error('');
                	return;
				}
				param.detail.forEach(a => {
					if (a.unitprice===0)
                	{
                    	commonUtil.valid.throwEx('0');
					}
					if(param.balanceWeightType==1){
						if(a.netWeight==0){
							commonUtil.valid.throwEx('0');
						}
						a.balancePreNum = a.netWeight;
					}
					else if(param.balanceWeightType==2){
						if(a.grossWeight==0){
							commonUtil.valid.throwEx('0');
						}
						a.balancePreNum = a.grossWeight;
					}else{
						if(a.poundWeight==0){
							commonUtil.valid.throwEx('0');
						}
						a.balancePreNum = a.poundWeight;
					}
					a.balanceNum=0;
					a.material=a.textureId;
					a.productPlace=a.producingId;
					a.createDate = param.createDate;
					a.transferNum=0;
					a.whsInFirstDate=$this.changDate(a.whsDayNum);					
				});
				this.$refs.pmTable.validator();
				httpUtil.post("BalanceList/addOtherBalanceList",param, data => {
                this.$message({
                    message: "",
                    type: 'success'
				});
				
				//
                $this.initFormState(data);
            });
                
			},
			settle:function(){
				if ((this.formModel.businessOtherNo)==="")
            	{
                	this.$message.error('');
            	}else{
					var details=this.$commonUtil.appConst.deepClone(this.dataSource);
					this.formModel.detailList=details;
					const costBalanceItem = {
                		component: costBalanceAddManage,
                		propsData:{entity:this.formModel},
                		name: "",
            		}
            		this.$tab.open(costBalanceItem);
				}				
			},
			changDate:function(AddDayCount){
				var dd=new Date();
				dd.setDate(dd.getDate()-AddDayCount);//AddDayCount
				var y = dd.getFullYear();
				var m = dd.getMonth()+1;//
				if(parseInt(m)<10){
					m="0"+m;
				}
				var d = dd.getDate();
				if(parseInt(d)<10){
					d="0"+d;
				}
				return y+"-"+m+"-"+d; //
			}
		}      
	}
</script>


