<template>
  <metro_page>
    <el-tabs type="border-card" v-model="activeName" @tab-click="handleClick">
      <el-tab-pane label="" name="main">
        <!--  -->
        <metro_page_box>
          <metro_page_box_body>
            <pm_search
              ref="pm_search"
              :packRowHeight="8"
              :hasPackNoSearch="false"
              :fields="searchFields"
              v-on:getAllSearchParams="queryData"
            ></pm_search>
          </metro_page_box_body>
        </metro_page_box>
        <!--  -->
        <metro_page_box>
          <pm_tool_bar>
            <pm_toolButton
              ref="editInvoice"
              btnName=""
              btnIcon="el-icon-edit-outline"
              :btnClickFunc="editInvoice"
            ></pm_toolButton>
            <pm_toolButton ref="edit" btnName="" btnIcon="el-icon-edit" :btnClickFunc="edit"></pm_toolButton>
            <pm_toolButton
              ref="remove"
              btnName=""
              btnIcon="el-icon-error"
              :btnClickFunc="remove"
            ></pm_toolButton>
            <pm_toolButton ref="print" btnName="" btnIcon="el-icon-printer" :btnClickFunc="print"></pm_toolButton>
            <pm_toolButton
              ref="export_btn"
              btnName=""
              btnIcon="el-icon-tickets"
              :btnClickFunc="export_btn"
            ></pm_toolButton>
          </pm_tool_bar>
          <!-- body -->
          <metro_page_box_body>
            <!-- table -->
            <pm_table
              ref="pmTable"
              tableKey="CostBalanceBookList-pmTable"
              :reload="afterSetting"
              v-if="reload"
              :dataSource="dataSource"
              :totalData="totalData"
              :config="config"
              :bottomHeight="210"
            >
              <pm_column prop="balanceNo" label="" width="200"></pm_column>
              <pm_column prop="balanceCompanyId" label="" width="200"></pm_column>
              <pm_column prop="cstId" label="" width="200"></pm_column>
              <pm_column prop="amtMoney" label="" width="200"></pm_column>
              <pm_column prop="actMoney" label="" width="200"></pm_column>
              <pm_column prop="businessNo" label="" width="200"></pm_column>
              <pm_column prop="balanceOtherNo" label="" width="200"></pm_column>
              <pm_column prop="balanceStatus" label="" width="200"></pm_column>
              <pm_column prop="balanceType" label="" width="200"></pm_column>
              <pm_column prop="payType" label="" width="200"></pm_column>
              <pm_column prop="creatorName" label="" width="200"></pm_column>
              <pm_column prop="createDate" label="" width="200"></pm_column>
              <pm_column prop="invoiceNo" label="" width="200" :cellCanEdit="cellCanEdit"></pm_column>
              <pm_column prop="invoiceType" label="" width="200" :cellCanEdit="cellCanEdit"></pm_column>
              <pm_column prop="remark" label="" width="200"></pm_column>
            </pm_table>
            <pm_pagination ref="pager" :totalSize="totalSize" :queryData="getBalanceMain"></pm_pagination>
          </metro_page_box_body>
        </metro_page_box>
      </el-tab-pane>
      <el-tab-pane label="" name="detail">
        <!--  -->
        <metro_page_box>
          <metro_page_box_body>
            <pm_search
              ref="pm_search1"
              :btnOffset="10"
              :packRowHeight="5"
              :packNoSearchSpan="6"
              :hasPackNoSearch="true"
              :fields="searchFieldsDetail"
              v-on:getAllSearchParams="queryData1"
            ></pm_search>
          </metro_page_box_body>
        </metro_page_box>
        <!--  -->
        <metro_page_box>
          <pm_tool_bar></pm_tool_bar>
          <!-- body -->
          <metro_page_box_body>
            <!-- table -->
            <pm_table
              ref="pmTable1"
              tableKey="CostBalanceBookList-pmTable1"
              :reload="afterSetting2"
              v-if="reload2"
              :dataSource="dataSource1"
              :totalData="totalData1"
              :config="config"
              :bottomHeight="210"
            >
              <pm_column prop="packNo" label="" width="180"></pm_column>
              <pm_column prop="brandId" label="" width="180"></pm_column>
              <pm_column prop="textureId" label="" width="150"></pm_column>
              <pm_column prop="spec" label="" width="150"></pm_column>
              <pm_column prop="whsDayNum" label="" width="150"></pm_column>
              <pm_column prop="whsFreeDayNum" label="" width="150"></pm_column>
              <pm_column prop="costType" label="" width="150"></pm_column>
              <pm_column prop="regularUnit" label="" width="150"></pm_column>
              <pm_column prop="settleUnitprice" label="" width="150"></pm_column>
              <pm_column prop="balanceNum" label="" width="150"></pm_column>
              <pm_column prop="settleExpress" label="" width="300"></pm_column>
              <pm_column prop="settleAmt" label="" width="150"></pm_column>
              <pm_column prop="changeMoney" label="" width="150"></pm_column>
              <pm_column prop="settleActAmt" label="" width="150"></pm_column>
              <pm_column prop="createDate" label="" width="150"></pm_column>
              <pm_column prop="businessType" label="" width="150"></pm_column>
              <pm_column prop="businessNo" label="" width="150"></pm_column>
              <pm_column prop="regularDetailNo" label="" width="150"></pm_column>
              <pm_column prop="oriPackNo" label="" width="150"></pm_column>
              <pm_column prop="cardNo" label="" width="150"></pm_column>
              <pm_column prop="balanceNoType" label="" width="150"></pm_column>
              <pm_column prop="remark" label="" width="250"></pm_column>
            </pm_table>
            <pm_pagination ref="pager1" :totalSize="totalSize1" :queryData="getBalanceDetail"></pm_pagination>
          </metro_page_box_body>
        </metro_page_box>
      </el-tab-pane>
    </el-tabs>
  </metro_page>
</template>

<script>
import metro_page from "@/components/vitems/pageContainer/metro_page";
import metro_page_box from "@/components/vitems/pageContainer/metro_page_box";
import metro_page_box_tool_bar from "@/components/vitems/pageContainer/metro_page_box_tool_bar";
import metro_page_box_body from "@/components/vitems/pageContainer/metro_page_box_body";
import metro_pageheader from "@/components/vitems/pageheader";
import pm_form_render from "@/components/common/form/pm_form_render";
import pm_form_item from "@/components/common/form/pm_form_item";
import pm_search from "@/components/vitems/pageSearch/pm_search";
import pm_toolButton from "@/components/common/button/pm_toolButton";
import pm_tool_bar from "@/components/common/table/pm_tool_bar";
import { tableReload } from "@/components/common/table/table-reload";
import pm_table from "@/components/common/table/pm_table";
import pm_column from "@/components/common/table/pm_column";
import httpUtil from "@/common/utils/HttpUtil";
import metadata from "@/common/entities/Metadata";
import commonUtil from "@/common/utils/CommonUtils";
import pm_pagination from "@/components/common/table/pm_pagination";
import costBalanceManage from "@/views/cost/costBalanceManage/CostBalanceManage";
import costInvoiceEdit from "./CostInvoiceEdit";
import cacheUtil from "@/common/utils/CacheUtil";
export default {
  mixins: [tableReload],
  components: {
    metro_page,
    metro_page_box,
    metro_page_box_tool_bar,
    metro_page_box_body,
    metro_pageheader,
    pm_search,
    pm_form_item,
    pm_toolButton,
    pm_tool_bar,
    pm_table,
    pm_column,
    pm_pagination
  },
  data: function() {
    return {
      dataSource: [],
      dataSource1: [],
      totalData: null,
      totalData1: null,
      config: {
        entity: metadata.regular,
        allowEdit: false,
        multiple: false,
        dbclick: this.doubleClick
        //   rowCanEdit: function(obj) {
        //     if (obj.cstName == null) return false;
        //     return obj.cstName.includes("");
        //     return false;
        //   }
      },
      totalSize: 0,
      totalSize1: 0,
      //
      activeName: "main",
      // 
      searchFields: [
        {
          displayName: "",
          fieldName: "balanceCompanyId",
          xtype: "search_select_input",
          searchLoc: "1-1-6-10-0"
        },
        {
          displayName: "",
          fieldName: "cstId",
          xtype: "search_select_input",
          searchLoc: "1-2-6-7-0"
        },
        {
          displayName: "",
          fieldName: "balanceType",
          xtype: "select",
          searchLoc: "1-3-6-9-0"
        },
        {
          displayName: "",
          fieldName: "businessNo",
          xtype: "text",
          searchLoc: "1-4-6-10-0"
        },
        {
          displayName: "",
          fieldName: "balanceNo",
          xtype: "text",
          searchLoc: "2-1-6-10-0"
        },
        {
          displayName: "",
          fieldName: "invoiceNo",
          xtype: "text",
          searchLoc: "2-2-6-8-0"
        },
        {
          displayName: "",
          fieldName: "businessOtherNo",
          xtype: "text",
          searchLoc: "2-3-6-10-0"
        },
        {
          displayName: "",
          fieldName: "balanceStatus",
          xtype: "select",
          searchLoc: "2-4-6-10-0"
        },
        {
          displayName: "",
          fieldName: "payType",
          xtype: "select",
          searchLoc: "3-1-6-10-0"
        },
        {
          displayName: "",
          fieldName: "IsSettleAmt",
          xtype: "radio",
          value: 1,
          searchLoc: "3-2-9-10-0",
          group: [
            { key: 0, value: "" },
            { key: 1, value: "" },
            { key: 2, value: "" }
          ]
        },
        {
          displayName: "",
          fieldName: "creatorName",
          xtype: "text",
          searchLoc: "3-3-9-15-0",
          value: cacheUtil.getUser().configUser.name,
          disabled: "true"
        },
        {
          displayName: "",
          fieldName: "createTime",
          xtype: "daterange",
          searchLoc: "4-1-12-5-0"
        },
        {
          displayName: "",
          fieldName: "search_btn",
          xtype: "btn",
          searchLoc: "4-2-5-1-1"
        }
      ],
      searchFieldsDetail: [
        {
          displayName: "",
          fieldName: "balanceCompanyId",
          xtype: "search_select_input",
          searchLoc: "1-1-8-10-0"
        },
        {
          displayName: "",
          fieldName: "cstId",
          xtype: "search_select_input",
          searchLoc: "1-2-8-10-0"
        },
        {
          displayName: "",
          fieldName: "balanceType",
          xtype: "select",
          searchLoc: "1-3-8-10-0"
        },
        {
          displayName: "",
          fieldName: "businessType",
          xtype: "select",
          searchLoc: "2-1-8-10-0"
        },
        {
          displayName: "",
          fieldName: "businessNo",
          xtype: "text",
          searchLoc: "2-2-8-10-0"
        },
        {
          displayName: "",
          fieldName: "invoiceNo",
          xtype: "text",
          searchLoc: "2-3-8-10-0"
        },
        {
          displayName: "",
          fieldName: "balanceStatus",
          xtype: "select",
          searchLoc: "3-1-8-10-0"
        },
        {
          displayName: "",
          fieldName: "balanceNo",
          xtype: "text",
          searchLoc: "3-2-8-10-0"
        },
        {
          displayName: "",
          fieldName: "businessOtherNo",
          xtype: "text",
          searchLoc: "3-3-8-10-0"
        },
        {
          displayName: "",
          fieldName: "creatorName",
          xtype: "text",
          searchLoc: "4-1-8-10-0",
          value: cacheUtil.getUser().configUser.name,
          disabled: "true"
        },
        {
          displayName: "",
          fieldName: "createTime",
          xtype: "daterange",
          searchLoc: "4-2-16-5-0"
        }
      ]
    };
  },
  mounted() {},
  methods: {
    handleClick: function(tab, event) {
      if (tab.name == "main") {
        this.afterSetting();
      } else {
        this.afterSetting2();
      }
    },
    queryData: function() {
      var $this = this;
      //1.
      this.getDataMain(data => {
        $this.totalData = data.content;
        //2.
        $this.$refs.pager.refreshData();
      });
    },
    queryData1: function() {
      var $this = this;
      this.getDataDetail(data => {
        $this.totalData1 = data.content;
        $this.$refs.pager1.refreshData();
      });
    },
    cellCanEdit: function(row, flag) {
      if (flag == 1) {
        return true;
      } else {
        return false;
      }
    },
    getBalanceMain: function(page, size) {
      var table = this;
      this.getDataMain(
        data => {
          table.dataSource = data.content;
          table.totalSize = data.total;
        },
        page,
        size
      );
    },
    /**
     * 
     */
    getDataMain(callback, page, size) {
      var table = this;
      var param = this.$refs.pm_search.getParam();
      param.page = page || 0;
      param.size = size || 100000;
      if (param.IsSettleAmt == 1) {
        param.IsHaveActMoney = true;
      } else if (param.IsSettleAmt == 2) {
        param.IsHaveActMoney = false;
      }
      httpUtil.post("balance/queryBalanceMain", param, data => {
        data.content.forEach(a => {
          if (a.payType == 0) {
            a.payType = null;
          }
        });
        if (callback) {
          callback(data);
        }
      });
    },
    /**
     * 
     */
    getBalanceDetail: function(page, size) {
      var table = this;
      this.getDataDetail(
        data => {
          table.dataSource1 = data.content;
          table.totalSize1 = data.total;
        },
        page,
        size
      );
    },
    getDataDetail: function(callback, page, size) {
      var table = this;
      var param = this.$refs.pm_search1.getParam();
      this.post(param, page, size, callback);
    },
    post: function(param, page, size, callback) {
      var $this = this;
      param.page = page || 0;
      param.size = size || 100000;
      httpUtil.post("balance/queryBalanceDetail", param, data => {
        data.content.forEach(a => {
          a.textureId = a.material;
          a.whsFreeDayNum = a.whsFreedayNum;
          a.settleUnitPrice = a.unitprice;
          if (!a.changeMoney) {
            a.changeMoney = 0;
            a.createDate = a.createTime;
          }
        });
        if (callback) {
          callback(data);
        }
        if ($this.activeName == "main") {
          $this.afterSetting();
        }
      });
    },
    doubleClick: function() {
      var $this = this;
      var selectItem = this.$refs.pmTable.currentRow;
      this.activeName = "detail";
      //
      $this.post(selectItem, 0, 25, data => {
        if (data.total > 25) {
          $this.post(selectItem, 0, 100000, data => {
            //
            $this.totalData1 = data.content;
            $this.dataSource1 = data.content;
            $this.totalSize1 = data.total;
          });
        } else {
          $this.totalData1 = data.content;
          $this.dataSource1 = data.content;
          $this.totalSize1 = data.total;
        }
        $this.afterSetting2();
      });
    },
    edit: function() {
      var selectItem = this.$refs.pmTable.currentRow;
      if (!selectItem) {
        this.$message.error("");
        return;
      }
      if (selectItem.invoiceNo != null && selectItem.invoiceNo != "") {
        this.$message.error("");
        return;
      }
      selectItem.size = 1000;
      selectItem.page = 0;
      var $this = this;
      this.$commonUtil.getEntity(
        "balance/queryBalanceMain",
        "balance/queryBalanceDetail",
        selectItem,
        function(entity) {
          $this.$tab.open({
            component: costBalanceManage,
            name: "",
            propsData: entity
          });
        }
      );
    },
    remove: function() {
      var $this = this;
      var selectItem = this.$refs.pmTable.currentRow;
      if (!selectItem) {
        this.$message.error("");
        return;
      }
      httpUtil.post("balance/deleteBalance", selectItem, data => {
        this.$message({
          message: "",
          type: "success"
        });
        $this.queryData();
      });
    },
    editInvoice: function() {
      var $this = this;
      var selectItem = this.$refs.pmTable.currentRow;
      if (!selectItem) {
        this.$message.error("");
        return;
      }
      this.$layer.iframe({
        content: {
          content: costInvoiceEdit, //
          parent: $this, //vue
          data: {
            entity: JSON.stringify(selectItem)
          }
        },
        area: ["400px", "220px"],
        shadeClose: false,
        title: ""
      });
    },
    export_btn: function() {
      var $this = this;
      this.getDataMain(data => {
        //
        var table = $this.$refs.pmTable;
        $this.$commonUtil.cloudExport(
          $this,
          data.content,
          table,
          ""
        );
      });
    },
    print: function() {}
  }
};
</script>


