<template>
  <metro_page>
    <!--  -->
    <metro_page_box>
      <metro_page_box_body>
        <!--  -->
        <pm_search
          ref="pm_search"
          :entity="entity"
          :btnOffset="10"
          :packNoSearchSpan="6"
          :hasPackNoSearch="true"
          :packRowHeight="4"
          :fields="searchFields"
          v-on:getAllSearchParams="queryData"
        ></pm_search>
        <pm_tool_bar>
          <pm_toolButton
            btnName=""
            btnPermission="btn_export"
            btnIcon="el-icon-document"
            :btnClickFunc="exportFunc"
          ></pm_toolButton>
        </pm_tool_bar>
      </metro_page_box_body>
    </metro_page_box>

    <!--  -->
    <metro_page_box>
      <!-- body -->
      <metro_page_box_body>
        <pm_table
          ref="pmTable"
          tableKey="NoteTransferGrid-pmTable"
          :reload="afterSetting"
          v-if="reload"
          :dataSource="dataSource"
          :totalData="totalData"
          :config="config"
        >
          <pm_column prop="transferNo" label="" width="120">
            <a
              href="javascript:void(0);"
              slot-scope="scope"
              @click="editTab(scope.row)"
            >{{scope.row.transferNo}}</a>
          </pm_column>
          <pm_column prop="cardNo" label="" width="150"></pm_column>
          <pm_column prop="transferStatus" label="" width="120"></pm_column>
          <pm_column prop="oldCstId" label="" width="200"></pm_column>
          <pm_column prop="newCstId" label="" width="200"></pm_column>
          <pm_column prop="mainRemark" label="" width="200"></pm_column>
          <pm_column prop="packNo" label="" width="150"></pm_column>
          <pm_column prop="whsinTime" label="" width="150"></pm_column>
          <pm_column prop="brandId" label="" width="150"></pm_column>
          <pm_column prop="textureId" label="" width="120"></pm_column>
          <pm_column prop="spec" label="" width="120"></pm_column>
          <pm_column prop="producingId" label="" width="150"></pm_column>
          <pm_column prop="calcType" label="" width="120"></pm_column>
          <pm_column prop="poundWeight" label="" width="120"></pm_column>
          <pm_column prop="measurementUnit" label="" width="120"></pm_column>
          <pm_column prop="amount" label="" width="120"></pm_column>
          <pm_column prop="unitNumber" label="" width="120"></pm_column>
          <pm_column prop="grossWeight" label="" width="120"></pm_column>
          <pm_column prop="netWeight" label="" width="120"></pm_column>
          <pm_column prop="whsCalcStackId" label="" width="150"></pm_column>
          <pm_column prop="manufactureTime" label="" width="150"></pm_column>
          <pm_column prop="stoveCode" label="" width="100"></pm_column>
          <pm_column prop="creatorName" label="" width="120"></pm_column>
          <pm_column prop="createTime" label="" width="150"></pm_column>
          <pm_column prop="costTime" label="" width="150"></pm_column>
        </pm_table>
        <pm_pagination ref="pager" :totalSize="totalSize" :queryData="getTransferInfo"></pm_pagination>
      </metro_page_box_body>
    </metro_page_box>
  </metro_page>
</template>

<script>
import metro_page from "@/components/vitems/pageContainer/metro_page";
import metro_page_box from "@/components/vitems/pageContainer/metro_page_box";
import metro_page_box_tool_bar from "@/components/vitems/pageContainer/metro_page_box_tool_bar";
import metro_page_box_body from "@/components/vitems/pageContainer/metro_page_box_body";
import metro_pageheader from "@/components/vitems/pageheader";
import pm_form_render from "@/components/common/form/pm_form_render";
import pm_form_item from "@/components/common/form/pm_form_item";
import pm_search from "@/components/vitems/pageSearch/pm_search";
import pm_toolButton from "@/components/common/button/pm_toolButton";
import pm_tool_bar from "@/components/common/table/pm_tool_bar";
import { tableReload } from "@/components/common/table/table-reload";
import pm_table from "@/components/common/table/pm_table";
import pm_column from "@/components/common/table/pm_column";
import noteTransferEdit from "./NoteTransferEdit";
import httpUtil from "@/common/utils/HttpUtil";
import metadata from "@/common/entities/Metadata";
import pm_pagination from "@/components/common/table/pm_pagination";
const statusEnum = {
  STATUS_READY: 1, //
  STATUS_SUCC: 2, //
  STATUS_DELETE: 3 //
};
const operTypeEnum = {
  OPER_ADD: "1", //
  OPER_EDIT: "2", //
  OPER_ADJUST: "3", //
  OPER_CHECK: "4" //
};
export default {
  components: {
    metro_page,
    metro_page_box,
    metro_page_box_tool_bar,
    noteTransferEdit,
    metro_page_box_body,
    metro_pageheader,
    pm_search,
    pm_form_item,
    pm_toolButton,
    pm_tool_bar,
    pm_table,
    pm_column,
    pm_pagination
  },
  mixins: [tableReload],
  data: function() {
    return {
      dataSource: [],
      totalData: null,
      entity: metadata.wmsTransfer,
      config: {
        entity: metadata.wmsTransferDetail,
        allowEdit: false
      },
      totalSize: 0,
      // 
      searchFields: [
        {
          displayName: "",
          fieldName: "transferNo",
          xtype: "text",
          searchLoc: "1-1-6-8-0"
        },
        {
          displayName: "",
          fieldName: "cardNo",
          xtype: "text",
          searchLoc: "1-2-6-8-0"
        },
        {
          displayName: "",
          fieldName: "stoveCode",
          xtype: "text",
          searchLoc: "1-3-6-8-0"
        },
        {
          displayName: "",
          fieldName: "oldCstId",
          xtype: "search_select_input",
          searchLoc: "1-4-6-8-0"
        },
        {
          displayName: "",
          fieldName: "newCstId",
          xtype: "search_select_input",
          searchLoc: "2-1-6-8-0"
        },
        {
          displayName: "",
          fieldName: "spec",
          xtype: "text",
          searchLoc: "2-2-6-8-0"
        },
        {
          displayName: "",
          fieldName: "brandId",
          xtype: "search_select_input",
          searchLoc: "2-3-6-8-0"
        },
        {
          displayName: "",
          fieldName: "textureId",
          xtype: "search_select_input",
          searchLoc: "2-4-6-8-0"
        },
        {
          displayName: "",
          fieldName: "producingId",
          xtype: "search_select_input",
          searchLoc: "3-1-6-8-0"
        },
        {
          displayName: "",
          fieldName: "netWeight",
          xtype: "number",
          searchLoc: "3-2-6-8-0"
        },
        {
          displayName: "",
          fieldName: "grossWeight",
          xtype: "number",
          searchLoc: "3-3-6-8-0"
        },
        {
          displayName: "",
          fieldName: "poundWeight",
          xtype: "number",
          searchLoc: "3-4-6-8-0"
        },
        {
          displayName: "",
          fieldName: "whsCalcStackId",
          xtype: "tree_select",
          searchLoc: "4-1-6-8-0"
        },
        {
          displayName: "",
          fieldName: "whsinTime",
          xtype: "date",
          searchLoc: "4-2-6-8-0"
        },
        {
          displayName: "",
          fieldName: "createTime",
          dateBegin: "createStartTime",
          dateEnd: "createEndTime",
          xtype: "datetimerange",
          searchLoc: "4-3-12-4-0"
        }
      ]
    };
  },
  methods: {
    queryData: function() {
      var $this = this;
      //1.
      this.getData(data => {
        $this.totalData = data.content;
        //2.
        $this.$refs.pager.refreshData();
      });
    },
    /**
     * 
     */
    getData(callback,page,size) {
      var $this = this;
      var param = this.$refs.pm_search.getParam();
      this.$commonUtil.handler.packNoHander(param);
      param.page = page || 0;
      param.size = size || 100000;
      httpUtil.post("whsTransfer/getLedgerDetailByPage", param, data => {
        if (callback) {
          callback(data);
        }
      });
    },
    /**
     * 
     */
    exportFunc: function() {
      var $this = this;
      this.getData(data => {
        //
        var table = $this.$refs.pmTable;
        $this.$commonUtil.cloudExport($this, data.content, table, "");
      });
    },
    // tab
    editTab: function(row, event) {
      let rowInfo = this.$commonUtil.deepClone(row);
      httpUtil.post("whsTransfer/getNoteByDetailId", rowInfo, data => {
        let openData = data;
        let titleName = "";
        //
        if (row.transferStatus == 1) {
          openData.method = operTypeEnum.OPER_EDIT;
          titleName = "[" + row.transferNo + "]";
        }
        //
        else if (row.transferStatus == 2) {
          openData.method = operTypeEnum.OPER_CHECK;
          titleName = "[" + row.transferNo + "]";
        }
        var $this = this;
        this.$commonUtil.getDetailEntity(
          "whsTransfer/getTransferDetail",
          { id: openData.id },
          openData,
          function(entity) {
            $this.$tab.open({
              component: noteTransferEdit,
              name: titleName,
              propsData: { entity, parentView: $this }
            });
          }
        );
      });
    },
    getTransferInfo: function(page, size) {
      var _this = this;
      this.getData(data => {
        _this.dataSource = data.content;
        _this.totalSize = data.total;
      },page,size);
    }
  }
};
</script>

<style scoped>
</style>



