<template>
  <metro_page>
    <!--  -->
    <metro_page_box>
      <metro_page_box_body>
        <!--  -->
        <pm_search
          ref="pm_search"
          :hasPackNoSearch="true"
          :packRowHeight="4"
          :btnOffset='10' 
          :packNoSearchSpan='6' 
          :fields="searchFields"
          :entity ="config.entity"
          v-on:getAllSearchParams="queryData"
        ></pm_search>
      </metro_page_box_body>
    </metro_page_box>

    <!--  -->
    <metro_page_box>
      <!-- toolbar -->
      <pm_tool_bar>
        <pm_toolButton
          btnName=""
          btnPermission="btn_export"
          btnIcon="el-icon-sold-out"
          :btnClickFunc="exportFunc"
        ></pm_toolButton>
      </pm_tool_bar>
      <!-- body -->
      <metro_page_box_body>
        <pm_table ref="pmTable" tableKey="NoteWhsStockGrid-pmTable" :reload="afterSetting" 
                  v-if="reload" :dataSource="dataSource" :totalData="totalData" :config="config">
          <!-- <vue_column :prop="props.field" slot-scope="props" label="" allowEdit='true' width="200" placeholder=""
          :callback="requestTransactionLogs" renderType="input"></vue_column>-->
          <pm_column prop="packNo" label="" width="150"></pm_column>
          <pm_column prop="cstId" label="" width="200"></pm_column>
          <pm_column prop="costDays" label="" width="100"></pm_column>
          <pm_column prop="brandId" label="()" width="150"></pm_column>
          <pm_column prop="textureId" label="()" width="110"></pm_column>
          <pm_column prop="spec" label="" width="120"></pm_column>
          <pm_column prop="producingId" label="" width="150"></pm_column>
          <pm_column prop="calcType" label="" width="100"></pm_column>
          <pm_column prop="measurementUnit" label="" width="100"></pm_column>
          <pm_column prop="netWeight" label="" width="100"></pm_column>
          <pm_column prop="realOutWeight" label="" width="100"></pm_column>
          <pm_column prop="grossWeight" label="" width="100"></pm_column>
          <pm_column prop="poundWeight" label="" width="100"></pm_column>
          <pm_column prop="number" label="" width="100"></pm_column>
          <pm_column prop="unitNumber" label="" width="100"></pm_column>
          <pm_column prop="whsCalcStackId" label="" width="160"></pm_column>
          <pm_column prop="whsStackName" label="" width="70"></pm_column>
          <pm_column prop="whsLayerId" label="" width="70"></pm_column>
          <pm_column prop="status" label="" width="80"></pm_column>
          <pm_column prop="remark" label="" width="200"></pm_column>
          <pm_column prop="manufactureTime" label="" width="140"></pm_column>
          <pm_column prop="stoveCode" label="" width="100"></pm_column>
          <pm_column prop="whsinTime" label="" width="140"></pm_column>
          <pm_column prop="costTime" label="" width="150"></pm_column>
          <pm_column prop="transferTimes" label="" width="100"></pm_column>
        </pm_table>
        <pm_pagination ref="pager" :totalSize="totalSize" :queryData="getList"></pm_pagination>
      </metro_page_box_body>
    </metro_page_box>
  </metro_page>
</template>
<script>
import metro_page from "@/components/vitems/pageContainer/metro_page";
import metro_page_box from "@/components/vitems/pageContainer/metro_page_box";
import metro_page_box_tool_bar from "@/components/vitems/pageContainer/metro_page_box_tool_bar";
import metro_page_box_body from "@/components/vitems/pageContainer/metro_page_box_body";
import metro_pageheader from "@/components/vitems/pageheader";
import Table from "@/components/common/table/Table";
import pm_form_render from "@/components/common/form/pm_form_render";
import pm_form_item from "@/components/common/form/pm_form_item";
import pm_search from "@/components/vitems/pageSearch/pm_search";
import pm_table from "@/components/common/table/pm_table";
import {tableReload} from '@/components/common/table/table-reload';
import pm_column from "@/components/common/table/pm_column";
import httpUtil from "@/common/utils/HttpUtil";
import metadata from "@/common/entities/Metadata";
import pm_pagination from "@/components/common/table/pm_pagination";
import pm_toolButton from "@/components/common/button/pm_toolButton";
import pm_tool_bar from "@/components/common/table/pm_tool_bar";
export default {
  components: {
    metro_page,
    metro_page_box,
    metro_page_box_tool_bar,
    metro_page_box_body,
    metro_pageheader,
    x_table: Table,
    pm_search,
    pm_form_item,
    pm_table,
    pm_column,
    pm_pagination,
    pm_toolButton,
    pm_tool_bar
  },
  mixins:[tableReload],
  data: function() {
    return {
      dataSource: [],
      totalData:null,
      config: {
        entity: metadata.wmsStock,
        allowEdit: false,
        multiple: false,
        autofit: false,//
      },
      totalSize: {
        type: Number,
        default: 0
      },
      // 
      searchFields: [
        {
          displayName: "",
          fieldName: "cardNo",
          xtype: 'text',
          searchLoc: "1-1-6-8-0"
        },
        {
          displayName: "()",
          fieldName: "brandId",
          xtype: 'search_select_input',
          searchLoc: "1-2-6-9-0"
        },
        {
          displayName: "()",
          fieldName: "textureId",
          xtype: 'search_select_input',
          searchLoc: "1-3-6-9-0"
        },
        {
          displayName: "",
          fieldName: "producingId",
          xtype: 'search_select_input',
          searchLoc: "1-4-6-8-0"
        },
        {
          displayName: "",
          fieldName: "cstId",
          xtype: 'search_select_input',
          searchLoc: "2-1-6-8-0"
        },
        {
          displayName: "",
          fieldName: "whsCalcStackId",
          xtype: 'tree_select',
          searchLoc: "2-2-6-8-0"
        },
        {
          displayName: "",
          fieldName: "spec",
          xtype: 'text',
          searchLoc: "2-3-6-8-0"
        },
        {
          displayName: "",
          fieldName: "netWeight",
          xtype: 'number',
          searchLoc: "2-4-6-8-0"
        },
        {
          displayName: "",
          fieldName: "grossWeight",
          xtype: 'number',
          searchLoc: "3-1-6-8-0"
        },
        {
          displayName: "",
          fieldName: "poundWeight",
          xtype: 'number',
          searchLoc: "3-2-6-8-0"
        },
        {
          displayName: "",
          fieldName: "whsinTime",
          dateBegin:"inTimeBegin",
          dateEnd:"inTimeEnd",
          xtype: 'datetimerange',
          timeDefault: [],
          searchLoc: "3-3-12-4-0"
        },
        {displayName: '', fieldName: 'status', xtype:'select_batch',value:[1,5], searchLoc:'4-1-12-4-0'}, 
      ]
    };
  },
  created(){
  },
  methods: {
    getParam: function() {
      this.$refs["demoForm"].validate();
    },
    /**
     * 
     */
    getList: function(page,size) {
      var $this = this;
      this.getData(data =>{
        $this.dataSource = Object.freeze(data.content);
        $this.totalSize = data.total;
      },page,size);
    },
    queryData:function(param){
      var $this = this;
      //1.
      this.getData(data =>{
        $this.totalData = data.content;
        //2.
        $this.$refs.pager.refreshData();
      });
    },
    /**
     * 
     */
    getData(callback,page,size){
      var $this = this;
      var param = this.$refs.pm_search.getParam();
      this.$commonUtil.handler.statusHander(param);
      this.$commonUtil.handler.packNoHander(param);
      param.page = page || 0;
      param.size = size || 100000;
      this.$httpUtil.post("stock/getList", param, data => {
        //
        if(data.content){
          data.content.forEach(a=>{
            $this.$metaData.wmsStock.methods.calcUsableProp(a);
          });
        }
        if(callback){
          callback(data);
        }
      });
    },
    /**
     * 
     */
    exportFunc: function(){
      var $this = this;
      this.getData(data=>{
        //
        var table = $this.$refs.pmTable;
        $this.$commonUtil.cloudExport($this,data.content,table,"");
      })
    }
  }
};
</script>


