<template>
  <metro_page>
    <!--  -->
    <metro_page_box>
      <metro_page_box_body>
        <!--  -->
        <pm_search
          ref="pm_search"
          :hasPackNoSearch="true"
          :packRowHeight="6"
          :btnOffset="10"
          :packNoSearchSpan="6"
          :fields="searchFields"
          :entity="config.entity"
          v-on:getAllSearchParams="queryData"
        ></pm_search>
      </metro_page_box_body>
    </metro_page_box>

    <!--  -->
    <metro_page_box>
      <!-- toolbar -->
      <pm_tool_bar>
        <pm_toolButton
          btnName=""
          btnPermission="btn_export"
          btnIcon="el-icon-document"
          :btnClickFunc="exportFunc"
        ></pm_toolButton>
      </pm_tool_bar>
      <!-- body -->
      <metro_page_box_body>
        <pm_table
          ref="pmTable"
          tableKey="NoteAdjustGrid-pmTable"
          :reload="afterSetting"
          v-if="reload"
          :dataSource="dataSource"
          :totalData="totalData"
          :config="config"
        >
          <!-- <vue_column :prop="props.field" slot-scope="props" label="" allowEdit='true' width="200" placeholder=""
          :callback="requestTransactionLogs" renderType="input"></vue_column>-->
          <pm_column prop="adjustNo" label="" width="120">
            <a
              href="javascript:void(0);"
              slot-scope="scope"
              @click="editWin(scope.row)"
            >{{scope.row.adjustNo}}</a>
          </pm_column>
          <pm_column prop="adjustType" label="" width="100"></pm_column>
          <pm_column prop="cstId" label="" width="200"></pm_column>
          <pm_column prop="status" label="" width="100"></pm_column>
          <pm_column prop="creatorName" label="" width="110"></pm_column>
          <pm_column prop="createTime" label="" width="140"></pm_column>
          <pm_column prop="mRemark" label="" width="200"></pm_column>
          <pm_column prop="cardNo" label="" width="120"></pm_column>
          <pm_column prop="packNo" label="" width="150"></pm_column>
          <pm_column prop="brandId" label="()" width="150"></pm_column>
          <pm_column prop="textureId" label="()" width="110"></pm_column>
          <pm_column prop="spec" label="" width="120"></pm_column>
          <pm_column prop="producingId" label="" width="150"></pm_column>
          <pm_column prop="calcType" label="" width="100"></pm_column>
          <pm_column prop="netWeight" label="" width="100"></pm_column>
          <pm_column prop="grossWeight" label="" width="100"></pm_column>
          <pm_column prop="poundWeight" label="" width="100"></pm_column>
          <pm_column prop="amount" label="" width="100"></pm_column>
          <pm_column prop="measurementUnit" label="" width="100"></pm_column>
          <pm_column prop="unitNumber" label="" width="100"></pm_column>
          <pm_column prop="whsCalcStackId" label="" width="160"></pm_column>
          <pm_column prop="whsStackName" label="" width="70"></pm_column>
          <pm_column prop="whsLayer" label="" width="70"></pm_column>
          <pm_column prop="manufactureTime" label="" width="140"></pm_column>
          <pm_column prop="stoveCode" label="" width="100"></pm_column>
          <pm_column prop="whsinTime" label="" width="140"></pm_column>
          <pm_column prop="costTime" label="" width="150"></pm_column>
          <pm_column prop="transferTimes" label="" width="100"></pm_column>
        </pm_table>
        <pm_pagination ref="pager" :totalSize="totalSize" :queryData="getList"></pm_pagination>
      </metro_page_box_body>
    </metro_page_box>
  </metro_page>
</template>
<script>
import metro_page from "@/components/vitems/pageContainer/metro_page";
import metro_page_box from "@/components/vitems/pageContainer/metro_page_box";
import metro_page_box_tool_bar from "@/components/vitems/pageContainer/metro_page_box_tool_bar";
import metro_page_box_body from "@/components/vitems/pageContainer/metro_page_box_body";
import metro_pageheader from "@/components/vitems/pageheader";
import Table from "@/components/common/table/Table";
import pm_form_render from "@/components/common/form/pm_form_render";
import pm_form_item from "@/components/common/form/pm_form_item";
import pm_search from "@/components/vitems/pageSearch/pm_search";
import pm_table from "@/components/common/table/pm_table";
import { tableReload } from "@/components/common/table/table-reload";
import pm_column from "@/components/common/table/pm_column";
import httpUtil from "@/common/utils/HttpUtil";
import metadata from "@/common/entities/Metadata";
import pm_pagination from "@/components/common/table/pm_pagination";
import pm_toolButton from "@/components/common/button/pm_toolButton";
import pm_tool_bar from "@/components/common/table/pm_tool_bar";
import commonUtil from "@/common/utils/CommonUtils";
import noteAdjustEdit from "./NoteAdjustEdit";
export default {
  components: {
    metro_page,
    metro_page_box,
    metro_page_box_tool_bar,
    metro_page_box_body,
    metro_pageheader,
    x_table: Table,
    pm_search,
    pm_form_item,
    pm_table,
    pm_column,
    pm_pagination,
    pm_toolButton,
    pm_tool_bar
  },
  mixins: [tableReload],
  data: function() {
    return {
      dataSource: [],
      totalData: null,
      config: {
        entity: metadata.wmsNoteAdjustDetail,
        allowEdit: false,
        multiple: false,
        extendConfig: {
          field: "old",
          props: [
            { prop: "adjustNo", label: "" },
            // {prop:"adjustType",label:""},
            { prop: "cstId", label: "" },
            { prop: "cardNo", label: "" },
            { prop: "packNo", label: "" },
            { prop: "brandId", label: "()" },
            { prop: "textureId", label: "()" },
            { prop: "spec", label: "" },
            { prop: "producingId", label: "" },
            { prop: "calcType", label: "" },
            { prop: "netWeight", label: "" },
            { prop: "grossWeight", label: "" },
            { prop: "poundWeight", label: "" },
            { prop: "amount", label: "" },
            { prop: "measurementUnit", label: "" },
            { prop: "unitNumber", label: "" },
            { prop: "whsCalcStackId", label: "" },
            { prop: "whsStackName", label: "" },
            { prop: "whsLayer", label: "" },
            { prop: "manufactureTime", label: "" },
            { prop: "stoveCode", label: "" },
            { prop: "whsinTime", label: "" },
            { prop: "costTime", label: "" },
            { prop: "transferTimes", label: "" }
          ]
        }
      },
      totalSize: {
        type: Number,
        default: 0
      },
      // 
      searchFields: [
        {
          displayName: "",
          fieldName: "adjustNo",
          xtype: "text",
          searchLoc: "1-1-6-8-0"
        },
        {
          displayName: "",
          fieldName: "cardNo",
          xtype: "text",
          searchLoc: "1-2-6-8-0"
        },
        {
          displayName: "",
          fieldName: "cstId",
          xtype: "search_select_input",
          searchLoc: "1-3-6-8-0"
        },
        {
          displayName: "()",
          fieldName: "brandId",
          xtype: "search_select_input",
          searchLoc: "1-4-6-9-0"
        },
        {
          displayName: "()",
          fieldName: "textureId",
          xtype: "search_select_input",
          searchLoc: "2-1-6-9-0"
        },
        {
          displayName: "",
          fieldName: "spec",
          xtype: "text",
          searchLoc: "2-2-6-8-0"
        },
        {
          displayName: "",
          fieldName: "producingId",
          xtype: "select",
          searchLoc: "2-3-6-8-0"
        },
        {
          displayName: "",
          fieldName: "whsCalcStackId",
          xtype: "tree_select",
          searchLoc: "2-4-6-8-0"
        },
        {
          displayName: "",
          fieldName: "stoveCode",
          xtype: "text",
          searchLoc: "3-1-6-8-0"
        },
        {
          displayName: "",
          fieldName: "netWeight",
          xtype: "number",
          searchLoc: "3-2-6-8-0"
        },
        {
          displayName: "",
          fieldName: "grossWeight",
          xtype: "number",
          searchLoc: "3-3-6-8-0"
        },
        {
          displayName: "",
          fieldName: "poundWeight",
          xtype: "number",
          searchLoc: "3-4-6-8-0"
        },
        {
          displayName: "",
          fieldName: "createTime",
          dateBegin: "createTimeStart",
          dateEnd: "createTimeEnd",
          xtype: "daterange",
          searchLoc: "4-1-12-4-0"
        },
        {
          displayName: "",
          timeDefault: [],
          fieldName: "whsinTime",
          dateBegin: "inTimeStart",
          dateEnd: "inTimeEnd",
          xtype: "daterange",
          searchLoc: "4-2-12-5-0"
        },
        {
          displayName: "",
          fieldName: "adjustType",
          xtype: "select",
          searchLoc: "5-1-6-8-0"
        }
        // {displayName: '', fieldName: 'btn_search', xtype:'btn', searchLoc:'4-4-16-8-0'}
        // {displayName: '', fieldName: 'status', xtype:'search_select_input', searchLoc:'2-1-8-8-0'},
      ]
    };
  },
  methods: {
    //
    editWin: function(row) {
      var $this = this;
      commonUtil.getEntity(
        "whsadjust/getList",
        "whsadjust/getDetailList",
        { page: 0, size: 1000, adjustId: row.adjustId },
        function(entity) {
          $this.$tab.open({
            component: noteAdjustEdit,
            name: "[" + row.adjustNo + "]",
            propsData: { key: row.adjustId, entity, parent: $this }
          });
        }
      );
    },
    getParam: function() {
      console.log(this.formModel);
      this.$refs["demoForm"].validate();
    },
    /**
     * 
     */
    getList: function(page, size) {
      //this.$layer.alert("");
      var param = this.$refs.pm_search.getParam();
      param.page = page;
      param.size = size * 2;
      this.$commonUtil.handler.packNoHander(param);
      var table = this;
      this.$httpUtil.post("whsadjust/getDetailList", param, data => {
        //
        var showList = data.content.filter(a => !a.ori);
        var oldList = data.content.filter(a => a.ori);
        showList.forEach(a => {
          a.old = oldList.find(item => item.relation == a.relation);
        });
        table.dataSource = showList;
        table.totalSize = data.total / 2;
        //this.$refs.pagination.totalSize = data.size
      });
    },
    queryData: function(param) {
      var $this = this;
      //1.
      this.getAllData(data => {
        $this.totalData = data;
        //2.
        $this.$refs.pager.refreshData();
      });
    },
    /**
     * 
     */
    getAllData(callback) {
      var $this = this;
      var param = this.$refs.pm_search.getParam();
      this.$commonUtil.handler.packNoHander(param);
      param.page = 0;
      param.size = 100000;
      this.$httpUtil.post("whsadjust/getDetailList", param, data => {
        if (callback) {
          //
          var showList = ((data || {}).content || []).filter(a => !a.ori);
          callback(showList);
        }
      });
    },
    /**
     * 
     */
    exportFunc: function() {
      var $this = this;
      this.getAllData(data => {
        //
        var table = $this.$refs.pmTable;
        $this.$commonUtil.cloudExport($this, data, table, "");
      });
    }
  }
};
</script>



