<template>
  <metro_page>
    <!--  -->
    <metro_page_box>
      <metro_page_box_body>
        <!--  -->
        <pm_search
          ref="pm_search"
          :btnOffset="10"
          :packNoSearchSpan="6"
          :hasPackNoSearch="true"
          :packRowHeight="5"
          :fields="searchFields"
          v-on:getAllSearchParams="queryData"
        ></pm_search>
        <pm_tool_bar>
          <pm_toolButton btnName="" btnIcon="el-icon-document" :btnClickFunc="exportFunc"></pm_toolButton>
        </pm_tool_bar>
      </metro_page_box_body>
    </metro_page_box>

    <!--  -->
    <metro_page_box>
      <!-- body -->
      <metro_page_box_body>
        <pm_table
          ref="pmTable"
          tableKey="NoteWhsOutGrid-pmTable"
          :reload="afterSetting"
          v-if="reload"
          :dataSource="dataSource"
          :totalData="totalData"
          :config="config"
        >
          <pm_column prop="whsoutNo" label="" width="150">
            <a
              href="javascript:void(0);"
              slot-scope="scope"
              @click="editTab(scope.row)"
            >{{scope.row.whsoutNo}}</a>
          </pm_column>
          <pm_column prop="remarkMain" label="" width="200"></pm_column>
          <pm_column prop="cstName" label="" width="200"></pm_column>
          <pm_column prop="deliveryName" label="" width="200"></pm_column>
          <pm_column prop="vehicleNumber" label="" width="120"></pm_column>
          <pm_column prop="status" label="" width="100"></pm_column>
          <pm_column prop="cstPackNo" label="" width="120"></pm_column>
          <pm_column prop="packNo" label="" width="150"></pm_column>
          <pm_column prop="brandId" label="" width="120"></pm_column>
          <pm_column prop="textureId" label="" width="120"></pm_column>
          <pm_column prop="spec" label="" width="120"></pm_column>
          <pm_column prop="producingId" label="" width="120"></pm_column>
          <pm_column prop="calcType" label="" width="100"></pm_column>
          <pm_column prop="measurementUnit" label="" width="100"></pm_column>
          <pm_column prop="number" label="" width="100"></pm_column>
          <pm_column prop="netWeight" label="" width="100"></pm_column>
          <pm_column prop="grossWeight" label="" width="100"></pm_column>
          <pm_column prop="poundWeight" label="" width="100"></pm_column>
          <pm_column prop="unitNumber" label="" width="100"></pm_column>
          <pm_column prop="whsCalcStackId" label="" width="200"></pm_column>
          <pm_column prop="stackName" label="" width="100"></pm_column>
          <pm_column prop="costDays" label="" width="100"></pm_column>
          <pm_column prop="transferTimes" label="" width="100"></pm_column>
          <pm_column prop="costTime" label="" width="150"></pm_column>
          <pm_column prop="floorNum" label="" width="100"></pm_column>
          <pm_column prop="slinger" label="" width="100"></pm_column>
          <pm_column prop="whsManager" label="" width="100"></pm_column>
          <pm_column prop="whsinTime" label="" width="150"></pm_column>
          <pm_column prop="transceiver" label="" width="150"></pm_column>
          <pm_column prop="stoveCode" label="" width="100"></pm_column>
          <pm_column prop="remark" label="" width="200"></pm_column>
          <pm_column prop="manufactureTime" label="" width="150"></pm_column>
          <pm_column prop="createTime" label="" width="150"></pm_column>
          <pm_column prop="creatorName" label="" width="150"></pm_column>
          <pm_column prop="packType" label="" width="150"></pm_column>
          <pm_column prop="packDesc" label="" width="150"></pm_column>
        </pm_table>
        <pm_pagination ref="pager" :totalSize="totalSize" :queryData="getList"></pm_pagination>
      </metro_page_box_body>
    </metro_page_box>
  </metro_page>
</template>

<script>
import metro_page from "@/components/vitems/pageContainer/metro_page";
import metro_page_box from "@/components/vitems/pageContainer/metro_page_box";
import metro_page_box_tool_bar from "@/components/vitems/pageContainer/metro_page_box_tool_bar";
import metro_page_box_body from "@/components/vitems/pageContainer/metro_page_box_body";
import metro_pageheader from "@/components/vitems/pageheader";
import pm_form_render from "@/components/common/form/pm_form_render";
import pm_form_item from "@/components/common/form/pm_form_item";
import pm_search from "@/components/vitems/pageSearch/pm_search";
import pm_toolButton from "@/components/common/button/pm_toolButton";
import pm_tool_bar from "@/components/common/table/pm_tool_bar";
import { tableReload } from "@/components/common/table/table-reload";
import pm_table from "@/components/common/table/pm_table";
import pm_column from "@/components/common/table/pm_column";
import httpUtil from "@/common/utils/HttpUtil";
import metadata from "@/common/entities/Metadata";
import pm_pagination from "@/components/common/table/pm_pagination";
import noteWhsOutEdit from "./NoteWhsOutEdit";
export default {
  mixins: [tableReload],
  components: {
    metro_page,
    metro_page_box,
    metro_page_box_tool_bar,
    metro_page_box_body,
    metro_pageheader,
    pm_search,
    pm_form_item,
    pm_toolButton,
    pm_tool_bar,
    pm_table,
    pm_column,
    pm_pagination
  },
  data: function() {
    return {
      dataSource: [],
      totalData: null,
      config: {
        entity: metadata.whsOut,
        allowEdit: false,
        multiple: false
      },
      totalSize: 0,
      // 
      searchFields: [
        {
          displayName: "",
          fieldName: "whsoutNo",
          xtype: "text",
          searchLoc: "1-1-6-8-0"
        },
        {
          displayName: "",
          fieldName: "cstId",
          xtype: "search_select_input",
          searchLoc: "1-2-6-8-0"
        },
        {
          displayName: "",
          fieldName: "cardNo",
          xtype: "text",
          searchLoc: "1-3-6-8-0"
        },
        {
          displayName: "",
          fieldName: "brandId",
          xtype: "search_select_input",
          searchLoc: "1-4-6-8-0"
        },
        {
          displayName: "",
          fieldName: "textureId",
          xtype: "search_select_input",
          searchLoc: "2-1-6-8-0"
        },
        {
          displayName: "",
          fieldName: "spec",
          xtype: "text",
          searchLoc: "2-2-6-8-0"
        },
        {
          displayName: "",
          fieldName: "producingId",
          xtype: "search_select_input",
          searchLoc: "2-3-6-8-0"
        },
        {
          displayName: "",
          fieldName: "netWeight",
          xtype: "number",
          searchLoc: "2-4-6-8-0"
        },
        {
          displayName: "",
          fieldName: "grossWeight",
          xtype: "number",
          searchLoc: "3-1-6-8-0"
        },
        {
          displayName: "",
          fieldName: "poundWeight",
          xtype: "number",
          searchLoc: "3-2-6-8-0"
        },
        {
          displayName: "",
          fieldName: "whsRoomId",
          xtype: "text",
          searchLoc: "3-3-6-8-0"
        },
        {
          displayName: "",
          fieldName: "stoveCode",
          xtype: "text",
          searchLoc: "3-4-6-8-0"
        },
        {
          displayName: "",
          fieldName: "vehicleNumber",
          xtype: "search_select_input",
          searchLoc: "4-1-6-8-0"
        },
        {
          displayName: "",
          fieldName: "whsinTime",
          xtype: "date",
          searchLoc: "4-2-6-8-0"
        },
        {
          displayName: "",
          fieldName: "createTime",
          xtype: "datetimerange",
          searchLoc: "4-3-12-4-0",
          dateBegin: "createTimeStart",
          dateEnd: "createTimeEnd"
        }
      ],
      searchParam: {}
    };
  },
  methods: {
    queryData: function(data) {
      this.searchParam = data;
      var $this = this;
      //1.
      this.getData(data => {
        $this.totalData = data.content;
        //2.
        $this.$refs.pager.refreshData();
      });
    },
    getList: function(page, size) {
      var table = this;
      this.getData(data => {
        table.dataSource = data.content;
        table.totalSize = data.total;
      },page,size);
    },
    // tab
    editTab: function(row, event) {
      var $this = this;
      this.$commonUtil.getEntityNoPageWithFileOut(
        "whsout/getOutInfoByMainId",
        "whsout/getWhsoutDetailInfoByMainId",
        "file/getFileItemInfo",
        { id: row.mainId, mainId: row.mainId, bizType: 2, bizId: row.mainId },
        function(entity) {
          entity.editFlag = "1";
          $this.$tab.open({
            component: noteWhsOutEdit,
            name: "[" + row.whsoutNo + "]",
            propsData: { key: row.id, entity, parentView: $this }
          });
        }
      );
    },
    /**
     * 
     */
    getData(callback,page,size) {
      var param = this.$refs.pm_search.getParam();
      this.$commonUtil.handler.packNoHander(param);
      param.page = page || 0;
      param.size = size || 100000;
      httpUtil.post("whsout/getOutDetailByPage", param, data => {
        if (callback) {
          callback(data);
        }
      });
    },
    /**
     * 
     */
    exportFunc() {
      var $this = this;
      this.getData(data => {
        //
        var table = $this.$refs.pmTable;
        $this.$commonUtil.cloudExport($this, data.content, table, "");
      });
    }
  }
};
</script>


