<template>
    <html :id="key">
    <head>
        <meta http-equiv="Content-Type" content="text/html; charset=UTF-8">
        <title></title>
        <meta http-equiv="X-UA-Compatible" content="IE=Edge,chrome=1">

    </head>
    <body>
   <div>
       <div class="cont" id="printDiv">
        <div class="title"></div>
        <div class="contractInfor clearfix">
            <ul class="fl">
                <li><label></label><span>{{mainInfo.whsoutNo}}</span></li>
                <li><label></label><span>{{mainInfo.whsInfo.name}}</span></li>
                <li><label></label><span>{{mainInfo.createTime}}</span></li>
            </ul>
            <ul class="fr">
                <li><label></label><span>{{mainInfo.cstId}}</span></li>
                <li><label></label><span>{{mainInfo.status}}</span></li>
                <li><label></label><span>{{mainInfo.vNum}}</span></li>
                <li><label></label><span>{{mainInfo.creatorName}}</span></li>
            </ul>
        </div>
        <div class="content" deep="1">
            <div class="text">
                <table class="contentTable" style="margin-top: 10px;">
                    <thead> 
                        <tr>
                            <th style="width: 35px;"></th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th>()</th>
                            <th style="width: 50px;"></th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr :key="item.id" v-for="(item,index) in detailInfo">  
                            <td >{{index+1}}</td>
                            <td >{{item.cstPackNo}}</td>
                            <td >{{item.packNo}}</td>
                            <td >{{item.brandId}}</td>
                            <td >{{item.textureId}}</td>
                            <td >{{item.spec}}</td>
                            <td >{{item.producingId}}</td>
                            <td >{{item.number}}</td>
                            <td >{{item.calcType}}</td>
                            <td >{{item.netWeight}}</td>
                            <td >{{item.stack}}</td>
                            <td >{{item.remark}}</td>
                        </tr>
                    </tbody>
                    <tfoot>
                        <tr>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td>{{mainInfo.allNumber}}</td>
                            <td></td>
                            <td>{{mainInfo.totalNetWeight}}</td>
                            <td></td>
                            <td></td>
                        </tr>
                    </tfoot>
                </table>
            </div>
        </div>
        <div class="bot">
            <table>
                <tbody>
                <tr>
                    <td>{{mainInfo.remark}}</td><td colspan="3"></td>
                </tr>
                <tr>
                    <td width="70"></td><td>{{mainInfo.whsInfo.name}}</td>
                    <td width="70"></td><td width="150">{{mainInfo.whsInfo.phone}}</td>
                </tr>
            </tbody></table>
        </div>
    </div>
    </div>
    </body>
    </html>
</template>
<script>
import cacheUtil from "@/common/utils/CacheUtil";
import componentUtil from "@/common/utils/ComponentUtil";
import metadata from "@/common/entities/Metadata";
export default {
    props: {
    },
    data:function(){
        return{
            config:{
                mainEntity:metadata.wmsTransfer,
                detailEntity:metadata.wmsTransferDetail,
                dicCache:null
            },
            mainInfo:{whsInfo:{name:''},stack:''},
            detailInfo:[],
            //
            isPrinting:false,
            key:this.$commonUtil.appConst.NewGuid(),
        }
    },
    methods: {
        printDiv:function(entity){
            //
            this.initData(entity);
            //
            this.isPrinting = true;
        },
        getDisplayModel(data,entity){
            //
            if(!this.config.dicCache){
                this.config.dicCache = cacheUtil.getDic()
            };
            let result = {};
            let stack = null;
            for(var item in data){
                let displayText = componentUtil.getDisplayText(
                    entity,
                    item,
                    data[item],
                    this.config.dicCache
                );
                result[item] = displayText; 
            }
            stack = componentUtil.getStackName(this.config.dicCache,data,'whsRoomId','whsAreaId');
            if(data['whsStackName']){
                stack = stack + ">" + data['whsStackName'];
            }
            if(data['whsLayer']){
                stack = stack + ">" + data['whsLayer'];
            }
            result['stack'] = stack;
            return result;
        },
        initData(entity){
            //
            let _this = this;
            this.detailInfo = entity.detailList.map(item =>_this.getDisplayModel(item,this.config.detailEntity));
            //
            this.mainInfo = this.getDisplayModel(entity,this.config.mainEntity);
            this.mainInfo.whsInfo = cacheUtil.getUser().accountInfo;
        }
    },
    updated(){
        if(this.mainInfo && this.isPrinting){
            printJS({
                printable: this.key,
                type: 'html',
                header: '',
                scanStyles:false,
                css: "/static/css/contract.css"

            });
        }
        this.isPrinting = false;
    },
}
</script>






