<template>
  <el-container>
    <el-aside width="240px" style="background-color:#fff;display:flex;
    flex-direction: column;margin:2px 5px 0 0">
      <p style="text-align:left;padding-left:24px;margin: 9px 0;"><b></b>
      <span>
        <el-button size="mini" title="" @click="addClass_func"></el-button>
        <!-- <el-button size="mini"></el-button>
        <el-button size="mini"></el-button> -->
      </span></p>
      <div class="block" style="overflow-y:auto;border-right: 1px solid rgb(238, 238, 238);flex:1">
      <el-tree :data="dicClassList" ref="mainTree" node-key="id" 
        @current-change="treeNodeChange"
        :expand-on-click-node="false"  
        :default-expand-all="true" 
        :check-strictly="true"  
        :highlight-current="true"
        :props="defaultProps">
        <span class="custom-tree-node" slot-scope="{ node, data }">
        <span>{{ node.label }}
           <el-button
            v-if="data.id !==0"
            type="text"
            size="mini"
            @click="() => editClass_func(data)">
            
          </el-button>
          <el-button
            v-if="data.id !==0"
            type="text"
            size="mini"
            @click="() => removeClass_func(data)">
            
          </el-button>
        </span>
      </span>
      </el-tree>
      </div>
      <!--  -->
      <!-- <pm_tree showField="name" keyField="id" :dicClassList="dicClassList" :getInfo="changeClass"></pm_tree> -->
      <!-- <el-menu
          class="el-menu-vertical-demo"
          unique-opened @open="getInfo" @close="getInfo">
        <pm_tree_item
        showField="name"
        keyField="id"
        :clickFunc="changeClass"
        :menus="dicClassList">
      </pm_tree_item>
      </el-menu>-->
    </el-aside>
    <el-container>
      <el-main style="padding: 0px;">
        <!--  -->
        <pm_search
          ref="pm_search"
          :hasPackNoSearch="false"
          :fields="searchFields"
          :entity="config.entity"
          v-on:getAllSearchParams="queryData"
        ></pm_search>
        <!-- toolbar -->
        <pm_tool_bar>
          <pm_toolButton
            btnName=""
            btnPermission="btn_add"
            btnIcon="el-icon-circle-plus-outline"
            :btnClickFunc="addWin"
          ></pm_toolButton>
          <pm_toolButton btnName="" btnPermission="btn_add" btnIcon="el-icon-edit" 
            :btnClickFunc="deleteInfo"></pm_toolButton>
        </pm_tool_bar>
        <pm_table ref="pmTable" :dataSource="dataSource" :config="config">
          <pm_column prop="name" label="" width="200"></pm_column>
          <pm_column prop="id" label="" width="200"></pm_column>
          <pm_column prop="mnemonicCode" label="" width="200"></pm_column>
          <pm_column prop="whsoutType" label="" width="200"></pm_column>
          <pm_column prop="classId" label="" width="200"></pm_column>
          <pm_column prop="deleteFlag" label="" width="200" placeholder></pm_column>
          <pm_column prop="remark" label="" width="200"></pm_column>
        </pm_table>
        <pm_pagination ref="pager" :totalSize="totalSize" :queryData="getList"></pm_pagination>
      </el-main>
    </el-container>
  </el-container>
</template>
<script>
import pm_form_render from "@/components/common/form/pm_form_render";
import pm_form_item from "@/components/common/form/pm_form_item";
import pm_search from "@/components/vitems/pageSearch/pm_search";
import pm_toolButton from "@/components/common/button/pm_toolButton";
import pm_tool_bar from "@/components/common/table/pm_tool_bar";
import pm_table from "@/components/common/table/pm_table";
import pm_column from "@/components/common/table/pm_column";
import httpUtil from "@/common/utils/HttpUtil";
import metadata from "@/common/entities/Metadata";
import pm_pagination from "@/components/common/table/pm_pagination";
import pm_dicMenu from "@/components/common/dicMenu/pm_dicMenu";
import pm_tree from "@/components/common/menu/pm_tree";
import pm_tree_item from "@/components/common/menu/pm_tree_item";

import commonUtil from "@/common/utils/CommonUtils";
import brandEdit from "./BrandEdit";
import brandClsEdit from './BrandClsEdit';

export default {
  components: {
    pm_search,
    pm_form_item,
    pm_toolButton,
    pm_tool_bar,
    pm_table,
    pm_column,
    pm_pagination,
    pm_dicMenu,
    pm_tree,
    pm_tree_item
  },
  data: function() {
    return {
      dicClassList: [],
      dataSource: [],
      selectClass:{},
      config: {
        entity: metadata.baseDictionary,
        dbclick: this.editWin
      },
      defaultProps: {
          children: 'children',
          label: 'name',
          id:'id'
      },
      // 
      searchFields: [
        {
          displayName: "",
          fieldName: "name",
          xtype: "text",
          searchLoc: "1-1-8-8-0"
        },
        {
          displayName: "",
          fieldName: "mnemonicCode",
          xtype: "text",
          searchLoc: "1-2-8-8-0"
        },
        {
          displayName: "",
          fieldName: "btn_search",
          xtype: "btn",
          searchLoc: "1-3-6-8-2"
        }
        // {displayName: '', fieldName: 'btn_search', xtype:'btn', searchLoc:'4-4-16-8-0'}
      ],
      totalSize: 0
      // [{"classCode":"CostType","className":"","type":3},{"classCode":"AdjustType","className":"","type":1},{"classCode":"BalanceNode","className":"","type":1},{"classCode":"BalanceNoType","className":"","type":1},{"classCode":"BalanceStatus","className":"","type":1},{"classCode":"BalanceType","className":"","type":1},{"classCode":"BrandSkuProperties","className":"sku","type":1},{"classCode":"BusinessType","className":"","type":1},{"classCode":"CalcType","className":"","type":1},{"classCode":"CstInTime","className":"","type":1},{"classCode":"CstSettleType","className":"","type":1},{"classCode":"CstType","className":"","type":1},{"classCode":"CstValid","className":"","type":1},{"classCode":"DataSource","className":"","type":1},{"classCode":"DeleteFlag","className":"","type":1},{"classCode":"InvoiceType","className":"","type":1},{"classCode":"MeasurementUnit","className":"","type":1},{"classCode":"MoveStatus","className":"","type":1},{"classCode":"NoteAdjustStatus","className":"","type":1},{"classCode":"NoteLockStatus","className":"","type":1},{"classCode":"PayType","className":"","type":1},{"classCode":"RangeApply","className":"","type":1},{"classCode":"RegularStatus","className":"","type":1},{"classCode":"RegularUnit","className":"","type":1},{"classCode":"SettleStatus","className":"","type":1},{"classCode":"SpecStr","className":"","type":1},{"classCode":"StockStatus","className":"","type":1},{"classCode":"StoreLocationType","className":"","type":1},{"classCode":"TransferStatus","className":"","type":1},{"classCode":"WareHouse","className":"","type":1},{"classCode":"WareHouseType","className":"","type":1},{"classCode":"WareHouseUserMax","className":"","type":1},{"classCode":"WeightType","className":"","type":1},{"classCode":"WhsInStatus","className":"","type":1},{"classCode":"WhsInStyle","className":"","type":1},{"classCode":"WhsInType","className":"","type":1},{"classCode":"WhsLevel","className":"","type":1},{"classCode":"whsoutStatus","className":"","type":1},{"classCode":"WhsoutType","className":"","type":1}]
    };
  },
  mounted: function() {
    this.loadTree();
  },
  methods: {
    loadTree:function(){
      var _this = this;
      this.$httpUtil.post("product/getBaseClassList", null, data => {
        //
        if(data){
          var root = {
            deleteFlag:false,
            id: 0,
            name: '',
            parentId:'',
            children:data
          };
          _this.dicClassList = [root];
        }
      });
    },
    /**
     * 
     */
    treeNodeChange:function(node,vm){
      this.selectClass = node;
      this.queryData({});
    },
    getSelectedClass:function(){
      var node = this.$refs.mainTree.getCurrentNode();
      if(node==null){
        this.$commonUtil.valid.throwEx("");
      }
      return node;
    },
    //----------  begin -----------------------
    /**
     * 
     */
    addClass_func: function(data) {
      var selectClass = this.getSelectedClass();
      var entity ={
        editFlag:'add',
        parentId: selectClass.id
      };
      this.$layer.iframe({
        content: {
          content: brandClsEdit, //
          parent: this,//vue
          data:{
            list:JSON.stringify(this.dicClassList),
            entity:JSON.stringify(entity)
          }//props
        },
        area:['400px','260px'],
        shadeClose: false, 
        title: ''
      });
    },
    /**
     * 
     */
    editClass_func: function(deptNode) {
      var entity = deptNode;
      entity.editFlag='edit';
      this.$layer.iframe({
        content: {
          content: brandClsEdit, //
          parent: this,//vue
          data:{
            list:JSON.stringify(this.dicClassList),
            entity:JSON.stringify(entity)
          }//props
        },
        area:['400px','260px'],
        shadeClose: false, 
        title: ''
      });
    },
    /**
     * 
     */
    removeClass_func: function(deptNode) { 
      var entity =deptNode; 
      var $this = this;
      this.$commonUtil.message.confirm(''+ entity.name +'?', function(){
        httpUtil.post("product/deleteBaseClass",entity, data => {
            this.getDepartmentTree();
            $this.message.alert('!',"success");
        });
      }); 
    },
    //----------------------------------------------------

    addWin: function() {
      var selectClass = this.getSelectedClass();
      this.$layer.iframe({
        content: {
          content: brandEdit, //
          parent: this, //vue
          data: { entity: JSON.stringify({ classId: selectClass.id })} //props
        },
        area: ["600px", "400px"],
        title: "",
        shadeClose: false, 
      });
    },
    //
    editWin: function(row, event) {
      var $this = this;
      this.$httpUtil.post("product/getBaseBrandInfo", row.id, entity => {
        $this.$layer.iframe({
          content: {
            content: brandEdit, //
            parent: $this, //vue
            data: { key: row.id, entity: JSON.stringify(entity) } //props
          },
          area: ["600px", "400px"],
          title: "[" + row.name + "]",
          shadeClose: false, 
        });
      });
    },
    //
    deleteInfo(){
        //id
        var row = this.$refs.pmTable.currentRow;
        if(!row){
          this.$commonUtil.valid.throwEx("");
        }
        let $this = this;
        this.$commonUtil.message.confirm('['+row.name+']?',()=>{
          //
          httpUtil.post("product/deleteBrand",row, data => {
              this.$message({
                  message: '',
                  type: 'success'
              });
              $this.refreshList();
          });
        });
    },
    /**
     * 
     */
    refreshList: function() {
      this.queryData();
    },
    queryData: function(param) {
      this.$refs.pager.refreshData(param);
    },
    getList: function(page, size,defaultParam) {
      //this.$layer.alert("");
      var param = null;
      if(defaultParam !=null){
        param = defaultParam;
      }else{
        param = this.$refs.pm_search.getParam();
      }
      param.page = page;
      param.size = size;
      var selectClass = this.$refs.mainTree.getCurrentNode();
      if(selectClass){
        //
        param.classId = selectClass.id;
      }else{
        param.classId = null;
      }
      var table = this;
      httpUtil.post("product/getBaseBrandList", param, data => {
        table.dataSource = data.content;
        table.totalSize = data.total;
        //this.$refs.pagination.totalSize = data.size
      },null,false);
    }
  }
};
</script>
<style scoped>
  .block .el-tree{
    background: transparent !important;
  }
</style>




