<template>
  <metro_page>
    <!--  -->
    <metro_page_box>
      <metro_page_box_body class="edit_padding">
        <pm_form_render
          :model="formModel"
          :rules="rules"
          ref="outForm"
          :entity="config.entity"
          v-bind:formReadOnly="formReadOnly"
        >
          <pm_form_item
            row="1"
            labletext=""
            name="whsoutNo"
            :span="6"
            xtype="text"
            lableWidth="100px"
            :readOnly="true"
          ></pm_form_item>
          <pm_form_item
            row="1"
            labletext=""
            name="cstId"
            :span="6"
            xtype="search_select_input"
            lableWidth="100px"
            required
          ></pm_form_item>
          <pm_form_item
            row="1"
            labletext=""
            name="deliveryId"
            :span="6"
            xtype="search_select_input"
            lableWidth="100px"
            required
            watchField="cstId"
          ></pm_form_item>
          <pm_form_item
            row="1"
            labletext=""
            name="vehicleNumber"
            :span="6"
            xtype="text"
            lableWidth="100px"
            required
          ></pm_form_item>
          <pm_form_item
            row="2"
            labletext=""
            name="billAttachmentId"
            :span="6"
            xtype="text"
            lableWidth="100px"
          ></pm_form_item>
          <pm_form_item
            row="2"
            labletext=""
            name="driverName"
            :span="6"
            xtype="text"
            lableWidth="100px"
            :rowHeight="3"
          ></pm_form_item>
          <pm_form_item
            row="2"
            labletext=""
            name="driverIdCard"
            :span="6"
            xtype="text"
            lableWidth="100px"
            :rowHeight="3"
          ></pm_form_item>
          <pm_form_item
            row="2"
            labletext=""
            name="driverPhone"
            :span="6"
            xtype="text"
            lableWidth="100px"
            :rowHeight="3"
          ></pm_form_item>
          <pm_form_item
            row="5"
            labletext=""
            name="status"
            :span="4"
            xtype="select"
            lableWidth="100px"
            :readOnly="true"
          ></pm_form_item>
          <pm_form_item
            row="5"
            labletext=""
            name="creatorName"
            :span="5"
            xtype="text"
            lableWidth="100px"
            :rowHeight="3"
            :readOnly="true"
          ></pm_form_item>
          <pm_form_item
            row="5"
            labletext=""
            name="createTime"
            :span="5"
            xtype="date"
            lableWidth="100px"
            :rowHeight="3"
            :readOnly="true"
          ></pm_form_item>
          <pm_form_item
            row="5"
            labletext=""
            name="modifierName"
            :span="5"
            xtype="text"
            lableWidth="100px"
            :rowHeight="3"
            :readOnly="true"
          ></pm_form_item>
          <pm_form_item
            ref="form_auditTime"
            :canEdit="auditTimeCanEdit"
            row="5"
            labletext=""
            name="auditTime"
            :span="5"
            xtype="datetime"
            lableWidth="100px"
            :rowHeight="3"
            :pickerOptions="pickerOptions"
          ></pm_form_item>
          <pm_form_item
            row="6"
            labletext=""
            name="remark"
            :span="24"
            xtype="textarea"
            lableWidth="100px"
            :rowHeight="2"
          ></pm_form_item>
        </pm_form_render>
        <pm_upload ref="upload" :fileList="formModel.fileList"></pm_upload>
        <pm_tool_bar>
          <pm_toolButton
            ref="select"
            btnName=""
            btnIcon="el-icon-circle-plus-outline"
            :btnClickFunc="getStock"
          ></pm_toolButton>
          <pm_toolButton
            ref="remove"
            btnName=""
            btnIcon="el-icon-remove-outline"
            :btnClickFunc="deleteRows"
          ></pm_toolButton>
        </pm_tool_bar>
      </metro_page_box_body>
    </metro_page_box>

    <!--  -->
    <metro_page_box>
      <!-- body -->
      <metro_page_box_body>
        <pm_table ref="pmTable" :dataSource="dataSource" :config="config" :bottomHeight="170">
          <pm_column prop="cstPackNo" label="" width="200" :cellCanEdit="cellCanEdit"></pm_column>
          <pm_column prop="packNo" label="" width="200" :cellCanEdit="cellCanEdit"></pm_column>
          <pm_column prop="brandId" label="" width="200" :cellCanEdit="cellCanEdit"></pm_column>
          <pm_column prop="textureId" label="" width="200" :cellCanEdit="cellCanEdit"></pm_column>
          <pm_column prop="spec" label="" width="150" :cellCanEdit="cellCanEdit"></pm_column>
          <pm_column prop="producingId" label="" width="200" :cellCanEdit="cellCanEdit"></pm_column>
          <pm_column prop="calcType" label="" width="100" :cellCanEdit="cellCanEdit"></pm_column>
          <pm_column prop="measurementUnit" label="" width="100" :cellCanEdit="cellCanEdit"></pm_column>
          <pm_column prop="number" label="" width="140" :cellCanEdit="cellCanEdit"></pm_column>
          <pm_column prop="netWeight" label="" width="140" :cellCanEdit="cellCanEdit"></pm_column>
          <pm_column prop="grossWeight" label="" width="140" :cellCanEdit="cellCanEdit"></pm_column>
          <pm_column prop="poundWeight" label="" width="140" :cellCanEdit="cellCanEdit"></pm_column>
          <pm_column prop="unitNumber" label="" width="100" :cellCanEdit="cellCanEdit"></pm_column>
          <pm_column
            prop="whsCalcStackId"
            label=""
            width="200"
            parentPropName="whsRoomId"
            childPropName="whsAreaId"
            :cellCanEdit="cellCanEdit"
          ></pm_column>
          <pm_column prop="stackName" label="" width="200" :cellCanEdit="cellCanEdit"></pm_column>
          <!-- <pm_column prop="costDays" label="" width="200" :cellCanEdit='cellCanEdit'></pm_column> -->
          <!-- <pm_column prop="transferTimes" label="" width="200" :cellCanEdit='cellCanEdit'></pm_column> -->
          <!-- <pm_column prop="costTime" label="" width="200" :cellCanEdit='cellCanEdit'></pm_column> -->
          <pm_column prop="floorNum" label="" width="120" :cellCanEdit="cellCanEdit"></pm_column>
          <pm_column prop="whsinTime" label="" width="200" :cellCanEdit="cellCanEdit"></pm_column>
          <pm_column prop="stoveCode" label="" width="200" :cellCanEdit="cellCanEdit"></pm_column>
          <pm_column prop="remark" label="" width="200" :cellCanEdit="cellCanEdit"></pm_column>
          <pm_column prop="manufactureTime" label="" width="200" :cellCanEdit="cellCanEdit"></pm_column>
          <pm_column prop="transceiver" label="" width="200" :cellCanEdit="cellCanEdit"></pm_column>
          <pm_column prop="slinger" label="" width="200" :cellCanEdit="cellCanEdit"></pm_column>
          <pm_column prop="whsManager" label="" width="200" :cellCanEdit="cellCanEdit"></pm_column>
          <!-- <pm_column prop="createTime" label="" width="200" :canEdit='false'></pm_column>
          <pm_column prop="creatorName" label="" width="200" :canEdit='false'></pm_column>-->
          <!-- <pm_column prop="packType" label="" width="200"></pm_column>
          <pm_column prop="packDesc" label="" width="200"></pm_column>-->
        </pm_table>
      </metro_page_box_body>
    </metro_page_box>

    <!--  -->
    <metro_page_box>
      <metro_page_box_body>
        <pm_tool_bar :noBackground="true">
          <pm_toolButton
            ref="btn_print"
            btnName=""
            btnIcon="el-icon-printer"
            :btnClickFunc="printInfo"
          ></pm_toolButton>
          <pm_toolButton ref="out" btnName="" btnIcon="el-icon-goods" :btnClickFunc="outWhs"></pm_toolButton>
          <pm_toolButton
            ref="cost"
            btnName=""
            btnIcon="el-icon-tickets"
            :btnClickFunc="settleInfo"
          ></pm_toolButton>
          <pm_toolButton
            ref="save"
            btnName=""
            btnIcon="el-icon-edit-outline"
            :btnClickFunc="saveInfo"
          ></pm_toolButton>
          <pm_toolButton ref="close" btnName="" btnIcon="el-icon-error" :btnClickFunc="closeWin"></pm_toolButton>
        </pm_tool_bar>
      </metro_page_box_body>
    </metro_page_box>
    <noteWhsOutPrint ref="print" v-show="false"></noteWhsOutPrint>
  </metro_page>
</template>

<script>
import metro_page from "@/components/vitems/pageContainer/metro_page";
import metro_page_box from "@/components/vitems/pageContainer/metro_page_box";
import metro_page_box_tool_bar from "@/components/vitems/pageContainer/metro_page_box_tool_bar";
import metro_page_box_body from "@/components/vitems/pageContainer/metro_page_box_body";
import metro_pageheader from "@/components/vitems/pageheader";
import pm_form_render from "@/components/common/form/pm_form_render";
import pm_form_item from "@/components/common/form/pm_form_item";
import pm_search from "@/components/vitems/pageSearch/pm_search";
import pm_toolButton from "@/components/common/button/pm_toolButton";
import pm_tool_bar from "@/components/common/table/pm_tool_bar";
import pm_table from "@/components/common/table/pm_table";
import pm_column from "@/components/common/table/pm_column";
import httpUtil from "@/common/utils/HttpUtil";
import metadata from "@/common/entities/Metadata";
import pm_pagination from "@/components/common/table/pm_pagination";
import cacheUtil from "@/common/utils/CacheUtil";
import layerUtil from "@/common/utils/LayerUtil";
import commonUtil from "@/common/utils/CommonUtils";
import pm_upload from "@/components/common/upload/pm_upload";
import printJS from "print-js";
import noteWhsOutPrint from "./NoteWhsOutPrint";
import costBalanceAddManage from "@/views/cost/costBalanceAdd/CostBalanceAddManage";
const statusEnum = {
  STATUS_READY: 1, //
  STATUS_SUCC: 2, //
  STATUS_DELETE: 3 //
};
const operTypeEnum = {
  OPER_ADD: "3", //
  OPER_EDIT: "1", //
  OPER_ADJUST: "2" //
};
export default {
  components: {
    metro_page,
    metro_page_box,
    metro_page_box_tool_bar,
    metro_page_box_body,
    metro_pageheader,
    pm_search,
    pm_form_item,
    pm_toolButton,
    pm_tool_bar,
    pm_table,
    pm_column,
    pm_pagination,
    pm_upload,
    noteWhsOutPrint
  },
  data: function() {
    var validPhone = (rule, value, callback) => {
      var reg = /^1[3|4|5|7|8][0-9]\d{8}$/;
      if (value && !reg.test(value)) {
        callback(new Error("11"));
      } else {
        callback();
      }
    };
    return {
      dataSource: [],
      config: {
        entity: metadata.whsOut,
        allowEdit: true,
        multiple: true,
        //   rowCanEdit: function(obj) {
        //   }
        change: function(row, prop, val) {
          var brand = cacheUtil
            .getDic()
            .BrandNameList.list.filter(item => item.id === row.brandId)[0];
          if (!brand) {
            return;
          }
          if (row.calcType != 1 || brand.whsoutType == 2) {
            //, 
            return;
          }
          if (prop == "number") {
            var newValue = row.number;
            row.netWeight = (newValue * row.netUnit).toFixed(3);
            row.grossWeight = (newValue * row.grossUnit).toFixed(3);
            row.poundWeight = (newValue * row.poundUnit).toFixed(3);
          }
          //   console.log('value = '+val+'  prop =' + prop);
        }
      },
      totalSize: 0,
      rules: {
        driverPhone: [{ trigger: "blur", validator: validPhone }]
      },
      formReadOnly: false,
      cutIdList: [],
      delPackNoList: [],
      formModel: Object,
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        }
      }
    };
  },
  created() {
    //
    this.formModel = this.getFormModel();
  },
  methods: {
    closeWin: function() {
      this.$commonUtil.tab.close(this);
    },
    //
    settleInfo() {
      var $this = this;
      var submitModel = JSON.parse(JSON.stringify(this.formModel));
      submitModel.detailList = this.dataSource;
      this.wrapData(submitModel);
      const costBalanceItem = {
        component: costBalanceAddManage,
        propsData: { entity: submitModel },
        name: ""
      };
      this.$tab.open(costBalanceItem);
    },
    //
    wrapData(entity) {
      entity.businessNo = entity.whsoutNo;
      entity.cstId = entity.cstId;
      entity.businessType = 2;
      entity.createDate = entity.createTime;
      entity.creatorData = entity.creator;
      if (!entity.detailList) {
        return;
      }
      entity.detailList.forEach(element => {
        element.material = element.textureId;
        element.productPlace = element.producingId;
        element.businessNo = element.whsoutNo;
        //element.transferDate = element.transferTime;
        element.whsInFirstDate = element.whsinTime;
        element.createDate = element.createTime;
        element.numFin = element.number;
        if (!element.transferTimes) {
          element.transferNum = 0;
        } else {
          element.transferNum = element.transferTimes;
        }
        element.businessType = 1;
        element.costDate = element.costTime;
      });
      entity.detail = entity.detailList;
    },
    //
    printInfo: function() {
      let _this = this;
      this.$commonUtil.getDetailEntity(
        "whsout/getWhsoutDetailInfoByMainId",
        {
          id: this.formModel.id,
          mainId: this.formModel.id,
          bizType: 2,
          bizId: this.formModel.id
        },
        this.formModel,
        function(entity) {
          _this.$refs.print.printDiv(entity);
        }
      );
    },
    //
    getFormModel: function() {
      var entity = this.$options.parent.propsData.entity;
      entity.detailList = entity.detail;
      this.$set(entity, "env", "bs");
      this.$set(entity, "driverPhone", null);
      if (entity.editFlag == operTypeEnum.OPER_ADD) {
        this.$set(
          entity,
          "auditTime",
          new Date()
        );
        entity.whsoutNo = this.$commonUtil.appConst.nullNo;
        entity.creatorName = cacheUtil.getUser().configUser.name;
        this.$set(entity, "status", 1);
        entity.canEditCols = [
          "number",
          "netWeight",
          "grossWeight",
          "poundWeight",
          "remark",
          "transceiver",
          "slinger",
          "whsManager"
        ];
      } else if (entity.editFlag == operTypeEnum.OPER_EDIT) {
        //TODO:
        // 
        this.formReadOnly = true;
        if (entity.status == statusEnum.STATUS_SUCC) {
          //
          this.config.allowEdit = false;
        } else {
          //
          entity.canEditCols = ["netWeight", "calcType", "remark"];
        }
        entity.detailList.forEach(element => {
          this.$set(element,"oriNetWeight",this.$commonUtil.deepClone(element.netWeight));
          this.$set(element,"oriCalcType",this.$commonUtil.deepClone(element.calcType));
          this.$set(element,"oriRemark",this.$commonUtil.deepClone(element.remark));
        });
        this.calcUnit(entity.detailList, false);
      } else if (entity.editFlag == operTypeEnum.OPER_ADJUST) {
        entity.canEditCols = [
          "number",
          "netWeight",
          "grossWeight",
          "poundWeight",
          "calcType",
          "remark",
          "transceiver",
          "slinger",
          "whsManager"
        ];
        this.calcUnit(entity.detailList, false);
      }
      this.dataSource = entity.detailList;
      return entity;
    },
    cellCanEdit: function(obj, prop) {
      var canEditCols = this.formModel.canEditCols;
      return canEditCols && canEditCols.includes(prop);
    },
    addDetail: function() {
      var $this = this;
      if (!$this.dataSource) {
        $this.dataSource = [];
      }
      var detail = {};
      this.counter += 1;
      detail.index = this.counter;
      $this.dataSource = $this.dataSource.concat(detail);
    },
    //
    deleteRows: function() {
      var $this = this;
      //
      var rows = this.$refs.pmTable.getSelectedRows();
      //
      commonUtil.removeSelectedRows(rows, this.dataSource, (pack, moveItem) => {
        return pack.packId == moveItem.packId;
      });
      rows.forEach(row => {
        if (row.id) {
          $this.deleteIdList.push(row.id);
          $this.delPackNoList.push(row.packNo);
        }
      });
    },
    //
    saveInfo: function() {
      this.$refs["outForm"].validate(this);
      //
      var $this = this;
      var cstList = cacheUtil.getDic().CstList.list;
      this.formModel.cstName = cstList.filter(
        item => item.id === this.formModel.cstId
      )[0].cstFullName;
      this.formModel.deliveryName = cstList.filter(
        item => item.id === this.formModel.deliveryId
      )[0].cstFullName;
      this.formModel.detail = this.dataSource;
      var files = this.$refs.upload.getNewFileList();
      if (this.formModel.editFlag == "3") {
        //
        httpUtil.postWithFile(
          "whsout/addWhsOut",
          this.formModel,
          files,
          data => {
            this.$message({
              message: "",
              type: "success"
            });
            this.$refs["save"].setDisabled(true);
            this.$refs["select"].setDisabled(true);
            this.$refs["remove"].setDisabled(true);
            this.$refs["upload"].setDisabled(true);
            this.$refs["out"].setDisabled(false);
            this.$refs["btn_print"].setDisabled(false);
            $this.$refs.upload.clearAllFile();
            this.formReadOnly = true;
            this.formModel.editFlag = operTypeEnum.OPER_EDIT;
            this.formModel.canEditCols = ["netWeight"];
            this.cellCanEdit();
            $this.updateTitle("[" + data.whsoutNo + "]");
            $this.handleBackData(data);
            $this.$options.parent.propsData.parentView.queryData();
          }
        );
      } else {
        //
        this.formModel.cutIdList = this.deleteIdList;
        this.formModel.delPackNoList = this.delPackNoList;
        var fileDeleteList = [];
        if (this.$refs.upload.getDeleteFileList().length > 0) {
          this.$refs.upload.getDeleteFileList().forEach(item => {
            fileDeleteList.push(item.id);
          });
        }
        this.formModel.fileDeleteList = fileDeleteList;
        httpUtil.postWithFile(
          "whsout/adjustWhsOut",
          this.formModel,
          files,
          data => {
            var titleHead = "";
            if (this.formModel.editFlag == operTypeEnum.OPER_ADJUST) {
              titleHead = "";
            }
            this.$message({
              message: titleHead + "",
              type: "success"
            });
            $this.$refs.upload.clearAllFile();
            $this.handleBackData(data);
            $this.$options.parent.propsData.parentView.queryData();
          }
        );
      }
    },
    handleBackData: function(data) {
      for (var p in data) {
        this.$set(this.formModel, p, data[p]);
      }
      this.dataSource = this.formModel.detail;
    },
    //
    getStock: function(filter, callback) {
      var $this = this;
      commonUtil.selProduct(this, { cstId: this.formModel.cstId }, function(
        stocks
      ) {
        //
        if (!$this.dataSource) {
          $this.dataSource = [];
        }
        $this.$options.methods.calcUnit(stocks, true);
        // 
        stocks.forEach(element => {
          if (
            $this.dataSource.length > 0 &&
            $this.dataSource.filter(item => item.packId === element.id).length > 0
          ) {
            //
            return;
          }
          element.packId = element.id;
          element.id = null;
          element.number = element.usableNum;
          element.netWeight = element.usableWeight;
          element.grossWeight = element.usableGrossWeight;
          element.poundWeight = element.usablePoundWeight;
          element.remark = null;
          element.stackName = element.whsStackName;
          element.floorNum = element.whsLayerId;
          $this.dataSource.push(element);
        });
      });
    },
    //
    calcUnit: function(details, isNew) {
      //isNew  
      var $this = this;
      if (isNew) {
        details.forEach(a => {
          commonUtil.calUnitWeight(
            a,
            a.usableWeight,
            a.usableGrossWeight,
            a.usablePoundWeight,
            a.usableNum
          );
        });
      } else {
        details.forEach(a => {
          commonUtil.calUnitWeight(
            a,
            a.netWeight,
            a.grossWeight,
            a.poundWeight,
            a.number
          );
        });
      }
    },
    //
    updateTitle: function(title) {
      this.$commonUtil.tab.updateTitle(this, title);
    },
    //
    outWhs: function() {
      var $this = this;
      //
      this.formModel.detail = this.dataSource;
      var weightChange = this.formModel.detail.filter(item => item.netWeight != item.oriNetWeight).length > 0;
      var calcTypeChange = this.formModel.detail.filter(item => item.calcType != item.oriCalcType).length > 0;
      var param = commonUtil.deepClone(this.formModel);
      param.status = 2;
      param.netWeightType = "out";
      var costModel = {};
      costModel.businessNo = this.formModel.whsoutNo;
      if (weightChange || calcTypeChange) {
        //
        this.$confirm("?", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }).then(() => {
          httpUtil.post("costCommon/queryCostBill", costModel, data => {
            var hasJs = data != null && data.Count > 0;
            if (hasJs) {
              this.$message({
                message: "",
                type: "fail"
              });
              return;
            } else {
              httpUtil.post("whsout/updateWhsOutDetail", param, data => {
                $this.handleBackData(data);
                httpUtil.post("whsout/stockOutInfo", param, data => {
                  $this.handleBackData(data);
                  this.$message({
                    message: "",
                    type: "success"
                  });
                  $this.$refs["out"].setDisabled(true);
                  this.updateTitle(
                    "[" + this.formModel.whsoutNo + "]"
                  );
                  $this.config.allowEdit = false;
                  $this.$options.parent.propsData.parentView.queryData();
                });
              });
            }
          });
        });
      } else {
        //
        httpUtil.post("whsout/stockOutInfo", param, data => {
          $this.handleBackData(data);
          this.$message({
            message: "",
            type: "success"
          });
          $this.$refs["out"].setDisabled(true);
          this.updateTitle("[" + this.formModel.whsoutNo + "]");
          $this.config.allowEdit = false;
          $this.$options.parent.propsData.parentView.queryData();
        });
      }
    }
  },
  computed: {
    /**
     * 
     */
    auditTimeCanEdit: function() {
      if (this.formModel.editFlag == operTypeEnum.OPER_ADJUST) {
        return true;
      } else if (this.formModel.editFlag == operTypeEnum.OPER_EDIT) {
        if (this.formModel.status == statusEnum.STATUS_SUCC) {
          return false;
        } else {
          return true;
        }
      } else {
        return true;
      }
    }
  },
  mounted() {
    /**
     * 
     */
    var hasSaveAuth = this.$commonUtil.permission.hasSaveAuth(this);
    var hasCheckAuth = this.$commonUtil.permission.hasCheckAuth(this);
    var hasPrintAuth = this.$commonUtil.permission.hasPrintAuth(this);
    if (!this.deleteIdList) {
      this.deleteIdList = [];
    }
    if (!this.delPackNoList) {
      this.delPackNoList = [];
    }
    //
    if (this.formModel.editFlag == operTypeEnum.OPER_ADJUST) {
      this.$refs["out"].setDisabled(true);
      this.$refs.form_auditTime.canEdit = true;
    } else if (this.formModel.editFlag == operTypeEnum.OPER_EDIT) {
      this.$refs["save"].setDisabled(true);
      this.$refs["select"].setDisabled(true);
      this.$refs["remove"].setDisabled(true);
      this.$refs["upload"].setDisabled(true);
      if (this.formModel.status == statusEnum.STATUS_SUCC) {
        this.$refs["out"].setDisabled(true);
        this.updateTitle("[" + this.formModel.whsoutNo + "]");
        this.$refs.form_auditTime.canEdit = false;
      } else {
        this.$refs.form_auditTime.canEdit = true;
      }
    } else {
      this.$refs["btn_print"].setDisabled(true);
      this.$refs["cost"].setDisabled(true);
      this.$refs["out"].setDisabled(true);
      this.$refs.form_auditTime.canEdit = true;
    }
    if (!hasSaveAuth) {
      this.$refs["add"].setDisabled(true);
    }
    if (!hasCheckAuth) {
      this.$refs["save"].setDisabled(true);
    }
    if (!hasPrintAuth) {
      this.$refs["btn_print"].setDisabled(true);
    }
  }
};
</script>


